<template>
    <div class="large-bar" ref="chart" style="height:100%;width:100%"></div>
</template>

<script>
import echarts from 'echarts'
import { DataCountHttp } from '../../api/dataCountHttp.js'
export default {
  name: 'SingleBar',
  props: {
    series: ''
  },
  data() {
    return {
      seriesName: [],
      seriesData: [],
      max: []
    }
  },
  watch: {
    series: {
      handler: function(val, oldVal) {
        this.initData()
      },
      deep: false
    }
  },
  methods: {
    initData() {
      this.seriesData = this.series.map((t, i) => {
        return t.value
      })
      this.seriesName = this.series.map((t, i) => {
        return t.name
      })
      const Max = Math.max(...this.seriesData)
      this.max = this.series.map((t, i) => {
        return Max
      })
      this.initChartOpts()
    },
    initChartOpts() {
      this.myChart = echarts.init(this.$refs.chart)
      let option = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
          },
          formatter: function(params) {
            return params[1].name + ':' + '<br />' + params[1].value
          }
        },
        grid: {
          top: '1%',
          left: '0%',
          right: '1%',
          bottom: '10%',
          containLabel: true
        },
        xAxis: [
          {
            type: 'category',
            axisTick: {
              show: false
            },
            axisLabel: {
              color: '#6b6f75',
              fontSize: 16
            },
            axisLine: {
              lineStyle: {
                color: '#bbe1f5',
                type: 'dashed'
              }
            },
            data: this.seriesName
          }
        ],
        yAxis: [
          {
            type: 'value',
            max: 'dataMax',
            axisLine: {
              show: false
            },
            axisTick: {
              show: false
            },
            splitLine: {
              lineStyle: {
                color: '#bbe1f5',
                type: 'dashed'
              }
            },
            axisLabel: {
              show: false
            }
          }
        ],
        series: [
          {
            name: '直接访问',
            type: 'bar',
            barGap: '-100%',
            barWidth: '50',
            silent: true,
            data: this.max,
            itemStyle: {
              color: 'rgba(187,225,245,.5)'
            }
          },
          {
            name: '直接访问',
            type: 'bar',
            barWidth: '50',
            data: this.seriesData,
            itemStyle: {
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                {
                  offset: 0,
                  color: '#4db8f5'
                },
                {
                  offset: 1,
                  color: '#257dd7'
                }
              ])
            }
          }
        ]
      }
      this.myChart.setOption(option)
    }
  },
  mounted() {
    this.initData()
  }
}
</script>

<style lang="scss" scoped>
</style>



