    methods:{
      /*start by wangbinyan*/
      getDateByDay(time) {
        let oDate = new Date(time);
        return '' + oDate.getFullYear() + '-' + toTen(oDate.getMonth() + 1) + '-' + toTen(oDate.getDate());
        function toTen(number) {
          return parseInt(number) < 10 ? '0' + number : '' + number;
        }
      },
      handleCommand(command){
        if(command == 'addKeyPerson'){
          this.visible1 = true
          //console.log('重点人员')
        }else if(command == 'addEquipment'){
          this.visible2 = true
          //console.log('设备')
        }else if(command == 'addPlace'){
          this.visible3 = true
          //console.log('场所')
        }
      },
      /**
       * 添加重点人员
       * */
      addKeyPeople(){
        console.log(this.KeyPeople.idc)
        if(this.KeyPeople.idc == ''){
          this.$message({
            message: '请输入身份证号',
            type: 'info'
          });
          return
        }
        seoHttp.addKeyPeople(this.KeyPeople).then(res => {
          this.$message({
            message: res.msg,
            type: 'success'
          });
          this.visible1 = false;
          this.KeyPeople={
            address: '',//现居地址
            birthday: '',//出生日期,
            buildNo: '',//楼栋号,
            cCity: '',//城市 ,
