            idc: '',//身份证号 ,
            length: '',//身高 ,
            name: '',//姓名 ,
            place: '',//户籍地 ,
            qq: '',//QQ ,
            sex: '',//性别 ,
            tel: '',//手机号 ,
            vx: ''//微信
          }
        }).catch(() => {});
      },
      /**
       * 添加设备
       * */
      addEquip(){
        /*
         * "equipmentNum":"adsssssssssssssssssss",
         "areaCode": "",
         "cityCode": "",
         "createTime": "",
         "equipmentName": "",
         "equipmentType": "",
         "mac": "",
         "provinceCode": "",
         "securityFactoryOrgcode": "",
         "serviceCode": ""
         * */
        if(this.equip.areaCode == ""){
          this.$message({
            message: '请选择所属地区',
            type: 'error'
          });
          return false
        }
        if(this.equip.equipmentName == ""){
          this.$message({
            message: '设备名称不能为空',
            type: 'error'
          });
          return false
        }
        if(this.equip.equipmentType == ""){
          this.$message({
            message: '设备类型不能为空',
            type: 'error'
          });
          return false
        }
        if(this.equip.mac == ""){
          this.$message({
            message: '设备mac地址不能为空',
            type: 'error'
          });
          return false
        }
        console.log(this.equip)
        this.equip.createTime = this.getDateByDay(new Date())
        seoHttp.addEquip(this.equip).then(res => {
          this.$message({
          message: res.msg,
          type: 'success'
        });
        this.visible2 = false
        this.equip={
          areaCode: '',//"区域编码-string",
          carCode: '',//"车牌号码-string",
