      },
      /**
       * 添加场所
       * */
      addPlace(){
        /*
         * {
         "areaCode": "",
         "businessNature": "",
         "capType": "",
         "cityCode": "",
         "createTime": "",
         "provinceCode": "",
         "serviceCode": "1234567890aadd",
         "serviceName": "",
         "status": "",
         "xpoint": "",
         "ypoint": ""
         }
         * */

        if(this.place.serviceName == ""){
          this.$message({
            message: '场所名称不能为空',
            type: 'error'
          });
          return false
        }
        if(this.place.areaCode == ""){
          this.$message({
            message: '所属地区不能为空',
            type: 'error'
          });
          return false
        }
        if(this.place.businessNature == ""){
          this.$message({
            message: '请选择场所性质',
            type: 'error'
          });
          return false
        }
        if(this.place.capType == ""){
          this.$message({
            message: '请选择场所性质',
            type: 'error'
          });
          return false
        }
        this.place.createTime = this.getDateByDay(new Date())
        console.log(this.place)
        this.place.startTime = this.place.time[0]
        this.place.endTime = this.place.time[1]
        seoHttp.addPlace(this.place).then(res => {
          this.$message({
          message: res.msg,
          type: 'success'
        });
        this.visible3 = false
        this.place={
          "address":'',//场所地址-string",
          "areaCode":'',//区域编码-string",
          "authAccount":'1',//网络认证账号或固定ip地址-string",
          "bandWidth":'',//带宽-string",
          "businessNature":'1',//场所性质-string",
          "capType":'1',//采集类型-string",
          "cityCode":'北京',//所属城市-string",
          "cityType":'1',//中心类型-string",
          "createTime":this.getDateByDay(new Date()),//创建时间-date",
          "endTime":'',//营业结束时间-string",
          "endingNumber":'1',//终端数-string",
          "exitIp":'192.168.100.11',//出口ip地址-string",
          "gisXpoint":'1',//场所在gis系统中的x坐标-string",
          "gisYpoint":'1',//场所在gis系统中的y坐标-string",
          "inforMan":'1',//信息安全员-string",
          "inforManEmail":'1',//安全员email地址-string",
          "inforManQq":'1',//信息安全员qq-string",
          "inforManTel":'1',//安全员联系电话-string",
          "ipType":'1',//ip分配方式-string",
          "isSafe":'1',//是否安全措施-string",
          "mailAccount":'115@qq.com',//接收邮箱地址-string",
          "mobileAccount":'1',//接收短信手机号码-string",
          "netLan":'',//下级网络-string",
          "netLanTerminal":'',//下级网络上网电脑-string",
          "netMonitorDepartment":'',//所属网监部门-string",
          "netMonitorMan":'',//网监负责人-string",
          "netMonitorManTel":'',//网监负责人联系电话-string",
          "netType":'1',//接入方式代码-string",
          "orgCode":'',//场所营业执照工商编码-string",
          "personName":'',//场所负责人-string",
          "personQq":'',//负责人qq-string",
          "personTel":'',//负责人电话-string",
          "policeCode":'',//派出所编码-string",
          "practitionerNumber":'',//从业人数-string",
          "principal":'',//负责人姓名-string",
          "principalCertCode":'',//法人证件号码-string",
          "principalCertType":'1',//法人证件类型-string",
          "principalTel":'',//负责人电话-string",
          "producerCode":'1',//接入服务运营商代码-string",
          "provinceCode":'1',//所属省-string",
          "remark":'1',//备注-string",
          "serverNumber":'1',//服务器数-string",
          "serviceCode":Date.parse(new Date()),//必填：场所编码-string",
          "serviceName":'',//场所名称-string",
          "serviceType":'1',//场所类型-string",
          "startTime":'',//营业开始时间-string",
          "status":'1',//场所服务状态代码-string",
          "terminalFactoryOrgcode":'1',//终端安全厂商组织机构编码-string",
          "terminalFactoryType":'1',//终端安全厂商类型-string",
          "wifiTerminal":'1',//无线接入用户数-string",
          "xpoint":'1',//场所地图经度-string",
          "ypoint":'1',//场所地图纬度-string",
