      /*end by wangbinyan*/


      initData(){
        this.options1 = this.optionsArea;
        this.options2 = this.optionsPlace;
        this.options3 = this.optionsEquipment;
      },
      searchMac(){
        seoHttp.searchMac({mac:this.input,areaCode:this.select1,
          name:this.input,
          serviceCode:this.select2,
          deviceNum:this.select3,
          page:this.page1.currentPage,
          pageSize:this.page1.size}).then((result)=>{
          this.tableData1 = result.data.features;
          this.page1.total = result.totalRecord;
          var ifr = document.getElementById("xl_search").contentWindow
          ifr.logicalFilterLayer();
        }).catch(() => {});
      },
      searchPlace(){
        seoHttp.searchPlace({serviceName:this.input,areaCode:this.select1,businessNature:this.select2,status:this.select3,page:this.page2.currentPage,pageSize:this.page2.size}).then((result)=>{
          this.tableData2 = result.data.features;
          this.page2.total = result.totalRecord;
          var ifr = document.getElementById("xl_search").contentWindow
          ifr.logicalFilterLayer();
        }).catch(() => {});
      },
      searchEquipment(){
        seoHttp.searchEquipment({equipmentNum:this.input,areaCode:this.select1,serviceCode:this.select2,vendorNum:this.select3,page:this.page3.currentPage,pageSize:this.page3.size}).then((result)=>{
          this.tableData3 = result.data.features;
          this.page3.total = result.totalRecord;
          var ifr = document.getElementById("xl_search").contentWindow
          ifr.logicalFilterLayer();
        }).catch(() => {});
      },
      searchKeyPerson(){
        seoHttp.searchKeyPerson({
//          searchStr:this.input,
          searchType:"",
          keyWord:this.input,
          areaCode:this.select1,
          type:this.select2,
          dept:this.select3,
          sex:this.select4,
          page:this.page4.currentPage,
          pageSize:this.page4.size}).then((result)=>{
          console.log("重点人",result)
          this.tableData4 = result.data.features;
          let arr=[];
          for(let item of result.data.features){
                arr.push(item.properties)
          }
          this.tableData4_copy=arr;
          this.page4.total = result.totalRecord;
          var ifr = document.getElementById("xl_search").contentWindow

          ifr.logicalFilterLayer();
        }).catch(() => {});
      },
      getMacByEquip(params){
        seoHttp.getMacByEquip(params).then(res => {
          var ifr2=document.querySelector("#xl_search").contentWindow;
          console.log("ifr2ghghjgfhgjhk",ifr2)
          ifr2.getMacsByDeviceId(res);
          this.macAll = res;
        })
      },
      getKeyPersonByEquip(params){
        seoHttp.getKeyPersonByEquip(params).then(res => {
          var ifr2=document.querySelector("#xl_search").contentWindow;
          ifr2.getKeysByDeviceId(res);
          this.keyAll = res;
        })
      },
      handleSelectionChange1(val) {
        this.multipleSelection1 = val;
      },
      handleSizeChange1(val) {
        this.page1.size = val;
        this.searchMac();
        // console.log(`每页 ${val} 条`);
      },
      handleCurrentChange1(val) {
        this.page1.currentPage = val;
        this.searchMac();
        // console.log(`当前页: ${val}`);
      },
      handleSelectionChange2(val) {
        this.multipleSelection2 = val;
      },
      handleSizeChange2(val) {
        this.page2.size = val;
        this.searchPlace();
        // console.log(`每页 ${val} 条`);
      },
      handleCurrentChange2(val) {
        this.page2.currentPage = val;
