      handleSizeChange3(val) {
        this.page2.size = val;
        this.searchEquipment();
        // console.log(`每页 ${val} 条`);
      },
      handleCurrentChange3(val) {
        this.page2.currentPage = val;
        this.searchEquipment();
        // console.log(`当前页: ${val}`);
