      handleSizeChange4(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange4(val) {
        console.log(`当前页: ${val}`);
      },
      search(){
        // this.tableBox = false;
        this.tableBoxBt = true;
        // this.tableBox2 = true;
        if(this.searchfalg == 1){
          this.tablefalg = 1;
          this.searchMac();
        }else if(this.searchfalg == 2){
          this.tablefalg = 2;
          this.searchPlace();
        }else if(this.searchfalg == 3){
          this.tablefalg = 3;
          this.searchEquipment();
        }else if(this.searchfalg == 4){
          this.tablefalg = 4;
          this.searchKeyPerson();
        }
      },
      macDialogCk(mac){
        seoHttp.searchMac({mac:mac}).then((result)=>{
          this.macForm = result.data.features[0].properties;
          this.macDialog = true;
        }).catch(() => {});
        seoHttp.macChart({mac:mac}).then((result)=>{
          this.echartsData.data = [];
          this.echartsData.ydata = [];
          for (let item of result.data) {
            this.echartsData.data.push(item.appearNum)
            this.echartsData.ydata.push(item.serviceName)
          }
        }).catch(() => {});
        this.macDialogMac = mac;
        let params={mac:this.macDialogMac,page:this.pageSwjl.currentPage,pageSize:this.pageSwjl.size}
        this.netWorkLog(params);
      },
      netWorkLog(params){
//        this.pageSwjl.currentPage = 1;
        seoHttp.netWorkLog(params).then((result)=>{
          this.macTable = result.data;
          this.pageSwjl.total=result.totalRecord;
          this.pageSwj2.total = Math.ceil(result.totalRecord/this.pageSwjl.size);
        }).catch(() => {});
      },
      handleSizeChangeSwjl(val) {
        this.pageSwjl.size = val;
        this.netWorkLog({mac:this.macDialogMac,page:this.pageSwjl.currentPage,pageSize:this.pageSwjl.size});
      },
      handleCurrentChangeSwjl(val) {
        this.pageSwjl.currentPage = val;
        this.netWorkLog({mac:this.macDialogMac,page:this.pageSwjl.currentPage,pageSize:this.pageSwjl.size});
      },
      handleCurrentChangeSwjl2(val) {
        this.pageSwj2.currentPage = val;
       this.netWorkLog({mac:this.macDialogMac,page:this.pageSwj2.currentPage,pageSize:this.pageSwj1.size});
      },
      placeDialogCk(placeName){
        seoHttp.searchPlace({serviceName:placeName}).then((result)=>{
          this.placeMap = result.data.features[0].properties;
          this.placeForm2 = result.data.features[0].properties;
          this.placeDialog = true;
          this.placeMap.features= [
          {
            type: "Feature", //不用改
            geometry: {
              type: "Point",//不用改
              coordinates: [this.placeMap.xpoint,this.placeMap.ypoint]  //x ，y
            },
            properties: {
              address: this.placeMap.address,
              areaInfos: null
            }
          }]
          console.log(this.placeMap)
        }).catch(() => {});
      },
      equipDialogCk(equipNum){
        seoHttp.searchEquipment({equipmentNum:equipNum}).then((result)=>{
          console.log("设备",result)
          this.equipForm = result.data.features[0].properties;
          this.equipDialog = true;
        }).catch(() => {});
      },
      keypDialogCk(idNum){
        this.keypDialog = true;
        seoHttp.serchKey({idc:idNum}).then((result)=>{
          console.log("重点人的相关",result)
          this.keypForm = result.data;
          if(this.keypForm.mac == '' || this.keypForm.mac == undefined || this.keypForm.mac == null){
            this.keypFormMacFlag = false;
          }else{
            this.keypFormMacFlag = true;
            seoHttp.searchMac({mac:this.keypForm.mac}).then((result)=>{
              this.macForm = result.data.features[0].properties;
            }).catch(() => {});
            seoHttp.macChart({mac:this.keypForm.mac}).then((result)=>{
              this.echartsData.data = [];
              this.echartsData.ydata = [];
              for (let item of result.data) {
                this.echartsData.data.push(item.appearNum)
                this.echartsData.ydata.push(item.serviceName)
              }
            }).catch(() => {});
            this.keypForm.mac = mac;
            this.netWorkLog();
          }
          console.log(this.keypForm)
        }).catch(() => {});
      },
      //加载地图
      handleClick(tab, event){

        console.log(tab.index)
        if(tab.index == '1'){
          /*document.getElementById('map_taskdetial').setAttribute('src', '../../../static/gis/xl_taskdetial.html');*/
          this.taskdetial = true

        }
      }
    },
    watch: {
      select: function (val, oldVal) {
        // console.log('new: %s, old: %s', val, oldVal)
        this.searchfalg = val;
        if(val == 1){
          this.select4falg = false;
          this.options1 = this.optionsArea;
          this.options2 = this.optionsPlace;
          this.options3 = this.optionsEquipment;
          this.select1 = '';
          this.select2 = '';
          this.select3 = '';
        }else if(val == 2){
          this.select4falg = false;
          this.options1 = this.optionsArea;
          this.options2 = this.optionsNature;
          this.options3 = this.optionsState;
          this.select1 = '';
          this.select2 = '';
          this.select3 = '';
        }else if(val == 3){
          this.select4falg = false;
          this.options1 = this.optionsArea;
          this.options2 = this.optionsPlace;
          this.options3 = this.optionsFirm;
          this.select1 = '';
          this.select2 = '';
          this.select3 = '';
        }else if(val == 4){
          this.select4falg = true;
          this.options1 = this.optionsArea;
          this.options2 = this.optionsCategory;
          this.options3 = this.optionsDepartment;
          this.options4 = this.optionsSex;
          this.select1 = '';
          this.select2 = '';
          this.select3 = '';
        }
      },
      select1: function (val, oldVal) {
        if(this.searchfalg == 1){
          seoHttp.selectPlace({cityCode:val}).then((result)=>{
            this.options2 = result.data;
          }).catch(() => {});
        }
      },
      select2: function (val, oldVal) {
        if(this.searchfalg == 1){
          seoHttp.selectEquip({serviceCode:val}).then((result)=>{
            this.options3 = result.data;
          }).catch(() => {});
        }
      },
      'equip.areaCode': function(val,oldVal){
        seoHttp.selectPlace({cityCode:val}).then((result)=>{
          this.optionsPlacePop = result.data;
        }).catch(() => {});
      }
    },
    components:{
      EchartsMac
    },
    mounted(){
      //console.log(window.vmObj())
      window.vmObj = () => {
        return this
      }
    }
   }
</script>

