<template>
  <div class="user-wrap">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>设置</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="manage-cont">

      <setupNav></setupNav>

      <div class="manage-nr">

        <div class="user-cont">
          <div class="user-l">
            <el-card class="box-card">
              <el-tree :data="treeData" :props="treeProps" @node-click="handleNodeClick"></el-tree>
            </el-card>
          </div>
          <div class="user-r">
            <el-card class="box-card">
              <el-button type="primary" size="small" @click="addUser">添加用户</el-button>
              <el-button type="primary" size="small" @click="alRole">分配角色</el-button>
              <el-input placeholder="请输入姓名" v-model="userName" size="small" class="input-with-seach ml20">
                <el-button slot="append" icon="el-icon-search" @click="searchList"></el-button>
              </el-input>
              <el-table
                class="mt20"
                ref="multipleTable"
                :data="tableData"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="handleSelectionChange"
                stripe>
                <el-table-column
                  type="selection"
                  prop="idCard"
                  width="50">
                </el-table-column>
                <el-table-column
                  label="序号"
                  type="index"
                  width="50">
                </el-table-column>
                <el-table-column
                  label="账号"
                  prop="idCard"
                  show-overflow-tooltip>
                </el-table-column>
                <el-table-column
                  label="姓名"
                  show-overflow-tooltip>
                  <template slot-scope="scope">
                    <el-button type="text" @click="getUser(scope.row.idCard)">{{scope.row.userName}}</el-button>
                  </template>
                </el-table-column>
                <el-table-column
                  prop="deptNm"
                  label="所属组织机构"
                  show-overflow-tooltip>
                </el-table-column>
                <el-table-column
                  prop="duty"
                  label="职务"
                  show-overflow-tooltip>
                </el-table-column>
                <el-table-column
                  prop="mobile"
                  label="电话"
                  show-overflow-tooltip>
                </el-table-column>
                <el-table-column
                  prop="sfCard"
                  label="身份证号"
                  show-overflow-tooltip>
                </el-table-column>
                <el-table-column
                  prop="remark"
                  label="备注"
                  show-overflow-tooltip>
                </el-table-column>
                <el-table-column
                  label="账号状态">
                  <template slot-scope="scope">
                    <span v-if="scope.row.legalNum == '1'">有效</span>
                    <span v-if="scope.row.legalNum == '2'">无效</span>
                  </template>
                </el-table-column>
                <el-table-column
                  fixed="right"
                  label="操作"
                  width="100">
                  <template slot-scope="scope">
                    <!-- <el-button type="text" size="small" v-if="scope.row.legalNum == '1'">停用</el-button>
                    <el-button type="text" size="small" v-if="scope.row.legalNum == '2'">启用</el-button> -->
                    <el-button type="text" size="small" v-if="scope.row.deleteStatus == '1'" @click="delUser(scope.row.id)">删除</el-button>
                  </template>
                </el-table-column>
              </el-table>
              <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="page.currentPage"
                :page-sizes="page.sizes"
                :page-size="page.size"
                layout="total, sizes, prev, pager, next, jumper"
                :total="page.total">
              </el-pagination>
            </el-card>
          </div>
        </div>


      </div>

    </div>

    <el-dialog
      title="添加用户"
      :visible.sync="addUserdialog"
      width="50%">
      <el-form ref="addUserFrom" :model="addUserFrom" label-width="120px">
        <el-form-item label="帐号：">
          <el-input v-model="addUserFrom.idCard"></el-input>
        </el-form-item>
        <el-form-item label="账号状态：">
          <el-radio-group v-model="addUserFrom.legalNum">
            <el-radio label="1">启用</el-radio>
            <el-radio label="2">停用</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="密码：">
          <!-- <el-input type="password" v-model="addUserFrom.password"></el-input> -->
          <el-input v-model="addUserFrom.password"></el-input>
        </el-form-item>
        <el-form-item label="姓名：">
          <el-input v-model="addUserFrom.userName"></el-input>
        </el-form-item>
        <el-form-item label="身份证号：">
          <el-input v-model="addUserFrom.sfCard"></el-input>
        </el-form-item>
        <el-form-item label="性别：">
          <el-radio-group v-model="addUserFrom.usex">
            <el-radio label="1">男</el-radio>
            <el-radio label="2">女</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="生日：">
          <el-date-picker
            v-model="addUserFrom.birthday"
            value-format="yyyyMMdd"
            type="date"
            placeholder="选择日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="联系电话：">
          <el-input v-model="addUserFrom.mobile"></el-input>
        </el-form-item>
        <el-form-item label="上级组织机构：">
          <el-cascader
            expand-trigger="click"
            placeholder="选择组织机构"
            :options="options"
            :props="optionsProps"
            change-on-select
            v-model="addSelectedOptions"
            @change="handleChange">
          </el-cascader>
        </el-form-item>
        <el-form-item label="职务：">
          <el-input v-model="addUserFrom.duty"></el-input>
        </el-form-item>
        <el-form-item label="备注：">
          <el-input type="textarea" v-model="addUserFrom.remark"></el-input>
        </el-form-item>
        <el-form-item label="角色：">
          <el-button size="small" @click="alRolePop">分配角色</el-button>
          <div>{{getRoleName}}</div>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addUserdialog = false">取 消</el-button>
        <el-button type="primary" @click="addUserSure">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="编辑用户"
      :visible.sync="editUserdialog"
      width="50%">
      <el-form ref="editUserFrom" :model="editUserFrom" label-width="120px">
        <el-form-item label="帐号：">
          <el-input v-model="editUserFrom.idCard" disabled></el-input>
        </el-form-item>
        <el-form-item label="账号状态：">
          <el-radio-group v-model="editUserFrom.legalNum">
            <el-radio label="1">启用</el-radio>
            <el-radio label="2">停用</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="密码：">
          <!-- <el-input type="password" v-model="editUserFrom.password"></el-input> -->
          <el-input v-model="editUserFrom.password"></el-input>
        </el-form-item>
        <el-form-item label="姓名：">
          <el-input v-model="editUserFrom.userName"></el-input>
        </el-form-item>
        <el-form-item label="身份证号：">
          <el-input v-model="editUserFrom.sfCard"></el-input>
        </el-form-item>
        <el-form-item label="性别：">
          <el-radio-group v-model="editUserFrom.usex">
            <el-radio label="1">男</el-radio>
            <el-radio label="2">女</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="生日：">
          <el-date-picker
            v-model="editUserFrom.birthday"
            value-format="yyyyMMdd"
            type="date"
            placeholder="选择日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="联系电话：">
          <el-input v-model="editUserFrom.mobile"></el-input>
        </el-form-item>
        <el-form-item label="上级组织机构：">
          <el-cascader
            expand-trigger="click"
            placeholder="选择上级组织机构"
            :options="options"
            :props="optionsProps"
            change-on-select
            v-model="editSelectedOptions"
            @change="handleChange">
          </el-cascader>
        </el-form-item>
        <el-form-item label="职务：">
          <el-input v-model="editUserFrom.duty"></el-input>
        </el-form-item>
        <el-form-item label="备注：">
          <el-input type="textarea" v-model="editUserFrom.remark"></el-input>
        </el-form-item>
        <el-form-item label="角色：">
          <el-button size="small" @click="alRolePop">分配角色</el-button>
          <div>{{getRoleName}}</div>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editUserdialog = false">取 消</el-button>
        <el-button type="primary" @click="editUserSure">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="分配角色"
      :visible.sync="alRoledialog"
      width="50%">
      <el-checkbox-group v-model="alRoleCheckbox">
        <el-checkbox v-for="popCheck in alRoleCheck" :label="popCheck.roleId" :key="popCheck.roleId">{{popCheck.roleName}}</el-checkbox>
      </el-checkbox-group>
      <span slot="footer" class="dialog-footer">
        <el-button @click="alRoledialog = false">取 消</el-button>
        <el-button type="primary" @click="alRoleSure">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="分配角色2"
      :visible.sync="alRolePopdialog"
      width="50%">
      <el-checkbox-group v-model="alRoleCheckbox">
        <el-checkbox v-for="popCheck in alRoleCheck" :label="popCheck.roleId" :key="popCheck.roleId">{{popCheck.roleName}}</el-checkbox>
      </el-checkbox-group>
      <span slot="footer" class="dialog-footer">
        <el-button @click="alRolePopdialog = false">取 消</el-button>
        <el-button type="primary" @click="alRolePopSure">确 定</el-button>
      </span>
    </el-dialog>

  </div>


</template>

<script type="text/javascript">
  import setupNav from 'components/setup/nav'
  import {userHttp} from '../../api/setupHttp.js';
  export default {
    name: 'home',
    data(){
      return {
        addSelectedOptions:[],
        editSelectedOptions:[],
        addUserFrom:{
          birthday: '',
          duty: '',
          idCard: '',
          id:'',
          legalNum: '',
          mobile: '',
          password: '',
          remark: '',
          sfCard: '',
          userName: '',
          usex: '',
          deptIdCard: '',
          role_id: [],
        },
        editUserFrom:{},
        addUserdialog: false,
        editUserdialog:false,
        alRoledialog:false,
        alRolePopdialog:false,
        options: [],
        optionsProps:{
          children: 'children',
          label: 'name',
          value: 'idCard',
        },
        option2: '',
        userName:'',
        idCard:'',
        tableData: [],
        multipleSelection:[],
        userIds:[],
        page:{
          currentPage:1,
          sizes:[10, 20],
          size:10,
          total:0
        },
        treeData: [],
        treeProps: {
          children: 'children',
          label: 'name'
        },
        alRoleCheck:[],
        alRoleCheckbox:[],
        getRoleName:''

      }
    },
    created:function(){
      this.initData();
    },
    methods:{
      initData(){
        this.getList();
        this.getTree();
        this.getRole();
      },
      getTree(){
        userHttp.getTree().then((result)=>{
          this.treeData = result.data;
          this.options = result.data;
        }).catch(() => {});
      },
      getRole(){
        userHttp.getRole().then((result)=>{
          this.alRoleCheck = result.data;
        }).catch(() => {});
      },
      getList(){
        userHttp.getList({page:this.page.currentPage,pageSize:this.page.size,userName:this.userName,idCard:this.idCard}).then((result)=>{
          this.tableData = result.data;
          this.page.total = result.totalRecord;
        }).catch(() => {});
      },
      searchList(){
        this.getList();
      },
      handleNodeClick(data) {
        this.idCard = data.idCard;
        this.getList();
      },
      handleChange(value) {
        // console.log(value);
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleSizeChange(val) {
        this.page.size = val;
        this.getList();
        // console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        this.page.currentPage = val;
        this.getList();
        // console.log(`当前页: ${val}`);
      },
      addUser(){
        this.addUserdialog = true;
      },
      alRolePop(){
        this.alRolePopdialog = true;
      },
      alRole(){
        if(this.multipleSelection.length == 0){
          this.$message({
            type: 'info',
            message: '请选择用户'
          });
          return
        }
        this.alRoleCheckbox = [];
        this.alRoledialog = true;
      },
      alRoleSure(){
        for (let item of this.multipleSelection) {
          this.userIds.push(item.id)
        }
        if(this.multipleSelection.length == 0){
          this.$message({
            type: 'info',
            message: '请选择角色或者取消分配角色'
          });
          return
        }
        userHttp.alRole({userIds:this.userIds,roleIds:this.alRoleCheckbox}).then((result)=>{
          this.alRoledialog = false;
          this.getList();
          this.alRoleCheckbox = [];
          this.$message({
            type: 'success',
            message: '分配角色成功!'
          });
        }).catch(() => {});
      },
      addUserSure(){
        this.addUserFrom.deptIdCard = this.addSelectedOptions.slice(-1).join();
        this.addUserFrom.role_id = this.alRoleCheckbox;
        this.addUserFrom.id = this.addUserFrom.idCard
        if(this.addUserFrom.usex == '1') this.addUserFrom.usex = true;
        if(this.addUserFrom.usex == '2') this.addUserFrom.usex = false;
        userHttp.addUser({jsonStr:this.addUserFrom}).then((result)=>{
          this.addUserdialog = false;
          this.getList();
          this.alRoleCheckbox = [];
          this.$message({
            type: 'success',
            message: '添加成功!'
          });
        }).catch(() => {});
      },
      getUser(idCard){
        if(this.addUserFrom.usex == true) this.addUserFrom.usex = '1';
        if(this.addUserFrom.usex == false) this.addUserFrom.usex = '2';
        userHttp.getUser({idCard:idCard}).then((result)=>{
          this.editUserdialog = true;
          this.editUserFrom = result.data;
        }).catch(() => {});
      },
      editUserSure(){
        this.editUserFrom.deptIdCard = this.editSelectedOptions.slice(-1).join();
        this.editUserFrom.role_id = this.alRoleCheckbox;
        if(this.editUserFrom.usex == '1') this.editUserFrom.usex = true;
        if(this.editUserFrom.usex == '2') this.editUserFrom.usex = false;
        userHttp.editUser({jsonStr:this.editUserFrom}).then((result)=>{
          this.editUserdialog = false;
          this.getList();
          this.$message({
            type: 'success',
            message: '修改成功!'
          });
        }).catch(() => {});
      },
      delUser(id){
        this.$confirm('是否删除该用户?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          userHttp.delUser({id:id}).then((result)=>{
            this.getList();
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
          }).catch(() => {});
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      alRolePopSure(){
        userHttp.getRoleName({ids:this.alRoleCheckbox.join()}).then((result)=>{
          this.getRoleName = result.data.join();
          this.alRolePopdialog = false;
          this.$message({
            type: 'success',
            message: '分配角色成功'
          });
        }).catch(() => {});
      }
    },
    components:{
      setupNav
    }
   }
</script>

<style lang="scss" scoped>
  @import "~assets/css/lib-variable";
  @import "~assets/css/lib-mixin";

  .user-wrap{
    width:  $site-width;
    min-width: $site-minwidth;
    min-height:$site-minheight;
    padding:0 20px;
    box-sizing:border-box;
    .user-cont{
      overflow:hidden;
      .user-l{float:left;width:250px;}
      .user-r{margin-left:260px;}
    }

    .poptit{font-size:14px;font-weight:bold;}
  }

</style>



