<template>
  <div class="task-detial">
    <el-dialog
      class="task-detial"
      :visible.sync="dialogDetial"
      width="60%" @close="closeDialog">
      <p class="opertaion" @click="del">删除</p>
      <div class="dialog-left">
        <table class="table">
          <tr>
            <td>任务名称：</td>
            <td>{{detiallist.task_name}}</td>
          </tr>
          <tr>
            <td>布控目标：</td>
           <td>{{detiallist.remards}}</td>
          </tr>
          <tr>
            <td>布控时间：</td>
            <td>{{detiallist.task_begin_time}}至{{detiallist.task_end_time}}</td>
          </tr>
        </table>
        <el-tabs v-model="activeName" @tab-click="handleClick" v-if="resArr.length!=0">
          <el-tab-pane :label="item.label" :name="item.name" v-for="item in resArr">
            <table class="table">
              <tr>
                <td>时间段：</td>
                <td>{{item.startTime}}至{{item.endTime}}</td>
              </tr>
              <tr>
                <td>出现次数：</td>
                <td>{{item.count}}</td>
              </tr>
              <tr>
                <td>场所范围：</td>
                <td v-if="item.serviceCodes.length>0">{{item.serviceCodes.join(',')}}</td>
                <td v-else></td>
              </tr>
              <!--<tr>-->
                <!--<td>发现目标：</td>-->
                <!--<td>{{}}人</td>-->
              <!--</tr>-->
            </table>
          </el-tab-pane>
        </el-tabs>
      </div>
      <div class="dialog-right" id="map_view">
        <iframe :style="{'height':100+'%','width':100+'%'}"  src="../../../static/gis/xl_taskdetial.html" />
      </div>
    </el-dialog>
  </div>
</template>
<script>
  import {Arrangement} from '../../api/collisionHttp'
  import {Collision} from '../../api/collisionHttp'
  export default {
    data() {
      return {
        detiallist:{task_name:'',target_type:'',task_begin_time:'',task_end_time:''},
        resArr:[],
        resDetial:[],
        coods:[],
        dialogDetial: false,
        activeName: "0",
      };
    },
    props:["outer","id","judge"],
    watch:{
      outer(newValue){
        this.dialogDetial=newValue;
      },
      id(newValue){
        if(newValue!=''){
          if(this.judge==1){
            //  布控管理页面触发的
            Arrangement.getTaskDetial({id:newValue}).then((result)=>{
              this.detiallist=result.data[0];
               this.coods=this.detiallist.result;
              if(this.detiallist.task_begin_time){
                this.detiallist.task_begin_time=this.getDateByDay(this.detiallist.task_begin_time);
              }
              if(this.detiallist.task_end_time){
                this.detiallist.task_end_time=this.getDateByDay(this.detiallist.task_end_time);
              }
              let obj=JSON.parse(this.detiallist.executecontrol_way_id);
              obj.forEach(function(val,index){
                val.name=index.toString();
                val.label="区域"+(index+1);
              })
              this.resArr=obj;
            }).catch(() => {});
          }
          if(this.judge==2){
            //  碰撞页面触发
            Collision.taskDetial({yigeID:newValue}).then((result)=>{
              this.detiallist=result.data[0];
              if(this.detiallist.task_begin_time){
                this.detiallist.task_begin_time=this.getDateByDay(this.detiallist.task_begin_time);
              }
              if(this.detiallist.task_end_time){
                this.detiallist.task_end_time=this.getDateByDay(this.detiallist.task_end_time);
              }
              let obj=JSON.parse(this.detiallist.executecontrol_way_id);
              obj.forEach(function(val,index){
                val.name=index.toString();
                val.label="区域"+(index+1);
              })
              this.resArr=obj;
            }).catch(() => {});
          }

        }
      }
    },
    methods: {
      getDateByDay(time) {
        let oDate = new Date(time);
        return '' + oDate.getFullYear() + '-' + toTen(oDate.getMonth() + 1) + '-' + toTen(oDate.getDate());
        function toTen(number) {
          return parseInt(number) < 10 ? '0' + number : '' + number;
        }
      },
      getAreaData(ind){
        let serviceCodes=this.resArr[ind].serviceCodes;
        let prototypes=Object.keys(this.coods);
        let that=this;
        serviceCodes.forEach(function(val,index){
          prototypes.forEach(function(val1,index1){
            if(val1==val){
              that.coods[val].push(val1)
              that.resDetial.push(that.coods[val]);
            }
          })
        })
        console.log("that.res",that.resDetial)
      },
      handleClick(tab, event) {
          this.getAreaData(tab.index);
      },
      closeDialog() {
        this.dialogDetial=false;
        this.$emit("choseDialog",false)
      },
      del(){
        if(this.judge==1){
          // 布控
          Arrangement.detelTask({id:this.id}).then((result)=>{
            if(res.code==200){
              this.$message({
                message: '删除成功',
                type: 'success'
              });
            }
            this.closeDialog();
          }).catch(() => {});
        }
        if(this.judge==2){
          //碰撞
          Collision.delTask({id:this.id}).then((res)=>{
            if(res.code==200){
              this.$message({
                message: '删除成功',
                type: 'success'
              });
            }
            this.closeDialog();
          }).catch((res)=>{})
        }

      }
    },
    created(){
      window.vmObj = () => {
        return this;
      };
    }
  };
</script>
<style lang="scss" scoped>
  .task-detial{
    .dialog-left{
      width:400px;
      float:left;
    }
    .dialog-right{
      height:400px;
      overflow: hidden;
    }
    .opertaion{
      text-align: right;
      font-size:14px;
      font-weight: 600;
      cursor: pointer;
    }
    .opertaion:hover{
      color:#409EFF;
    }
    .table{
      width:450px;
      tr{
        height:30px;
      }
      td:first-child{
        width:100px;
        text-align: right;
      }
    }
  }

</style>



