<template>
    <div class="single-bar" ref="chart" style="height:100%;width:100%"></div>
</template>

<script>
import echarts from 'echarts'
import {homeHttp} from '../../api/homeHttp.js';
export default {
    name:'SingleBar',
     data(){
      return {
          seriesName:[],
          seriesData:[],
          max:[]
      }
    },
    methods:{
      initData(){
        this.getBkList();
      },
      getBkList(){
        homeHttp.collectMacNum().then((result)=>{
          this.seriesName = result.data.historyWeekCount.map((t,i)=>{
              return t.weeks
          })
          this.seriesData = result.data.historyWeekCount.map((t,i)=>{
              return t.count
          })
          const Max = Math.max(...this.seriesData)
          this.max = result.data.historyWeekCount.map((t,i)=>{
              return Max
          })
          this.initChartOpts()
        }).catch(() => {});
      },
      initChartOpts(){
          this.myChart = echarts.init(this.$refs.chart);
          let option = {
            tooltip : {
                trigger: 'axis',
                axisPointer : {        
                    type : 'shadow'  
                },
                formatter: function (params) {
                    var tar = params[1];
                    return tar.name +  ' : ' + tar.value;
                }
            },
            grid: {
                top:'6%',
                left: '6%',
                right: '6%',
                bottom: '10%',
                containLabel: true
            },
            xAxis : [
                {
                type : 'category',
                axisTick:{
                    show:false
                },
                axisLabel:{
                    color:'#000'
                },
                axisLine:{
                    lineStyle:{
                    color:'#d2d2d2'
                    }
                },
                data : this.seriesName,
                }
            ],
            yAxis : [
                {
                type : 'value',
                max:'dataMax',
                axisLine:{
                    show:false
                },
                axisTick:{
                    show:false
                },
                axisLabel:{
                    color:'#000'
                }
                }
            ],
            series : [
                {
                name:'本周采集',
                    type:'bar',
                    barGap:'-100%',
                    barWidth: '30',
                    silent: true,
                    data:this.max,
                    itemStyle:{
                    color: 'rgba(246,246,246,.7)',
                    }
                },
                {
                name:'本周采集',
                    type:'bar',
                    barWidth: '30',
                    data:this.seriesData,
                    itemStyle:{
                    color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                        offset: 0,
                        color: '#4db8f5'
                    }, {
                        offset: 1,
                        color: '#257dd7'
                    }])
                    }
                }
            ]
        }
       this.myChart.setOption(option)
      }
    },
    mounted(){
        this.initData()
    }
}
</script>

<style lang="scss" scoped>
</style>



