<template>
  <div class="organ-wrap">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>设置</el-breadcrumb-item>
      <el-breadcrumb-item>组织机构管理</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="manage-cont">

      <setupNav></setupNav>

      <div class="manage-nr">
        <div class="organ-tree">
          <el-card class="box-card">
            <el-tree :data="treeData" :props="treeProps" @node-click="handleNodeClick"></el-tree>
          </el-card>
        </div>
        <div class="organ-cont">
          <el-card class="box-card">
            <el-button type="primary" size="small" @click="addOrgan">添加组织机构</el-button>
            <el-input placeholder="请输入组织机构名称" v-model="deptName" class="input-with-seach ml20" size="small">
              <el-button slot="append" icon="el-icon-search" @click="searchList"></el-button>
            </el-input>
            <el-table
              class="mt20"
              :data="tableData"
              tooltip-effect="dark"
              style="width: 100%"
              stripe>
              <el-table-column
                label="序号"
                type="index"
                width="50">
              </el-table-column>
              <el-table-column
                label="名称"
                show-overflow-tooltip>
                <template slot-scope="scope">
                  <el-button type="text" @click="getRole(scope.row.id)">{{scope.row.name}}</el-button>
                </template>
              </el-table-column>
              <el-table-column
                label="简称"
                prop="shortName"
                show-overflow-tooltip>
              </el-table-column>
              <el-table-column
                label="备注"
                prop="remark"
                show-overflow-tooltip>
              </el-table-column>
              <!-- <el-table-column
                label="更新时间"
                prop="updateTime"
                :formatter="getLocalTime"
                show-overflow-tooltip>
              </el-table-column> -->
              <el-table-column
                  fixed="right"
                  label="操作"
                  width="100">
                  <template slot-scope="scope">
                    <el-button type="text" size="small" v-if="scope.row.deleteStatus == '1'" @click="delOrgan(scope.row.id)">删除</el-button>
                  </template>
                </el-table-column>
            </el-table>
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="page.currentPage"
              :page-sizes="page.sizes"
              :page-size="page.size"
              layout="total, sizes, prev, pager, next, jumper"
              :total="page.total">
            </el-pagination>
          </el-card>
        </div>
      </div>

    </div>

    <el-dialog
      title="添加组织机构"
      :visible.sync="addOrgandialog"
      width="50%">
      <el-form ref="addOrganFrom" :model="addOrganFrom" label-width="120px">
        <el-form-item label="组织机构名称：">
          <el-input v-model="addOrganFrom.name"></el-input>
        </el-form-item>
        <el-form-item label="组织机构编码：">
          <el-input v-model="addOrganFrom.idCard"></el-input>
        </el-form-item>
        <el-form-item label="简称：">
          <el-input v-model="addOrganFrom.shortName"></el-input>
        </el-form-item>
        <el-form-item label="备注：">
          <el-input type="textarea" v-model="addOrganFrom.remark"></el-input>
        </el-form-item>
        <el-form-item label="上级组织机构：">
          <el-cascader
            expand-trigger="click"
            placeholder="选择上级组织机构"
            :options="options"
            :props="optionsProps"
            change-on-select
            v-model="addOrganFrom.selectedOptions"
            @change="handleChange">
          </el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addOrgandialog = false">取 消</el-button>
        <el-button type="primary" @click="addOrganSure">保 存</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="编辑组织机构"
      :visible.sync="editOrgandialog"
      width="50%">
      <el-form ref="editOrganFrom" :model="editOrganFrom" label-width="120px">
        <el-form-item label="组织机构名称：">
          <el-input v-model="editOrganFrom.name"></el-input>
        </el-form-item>
        <el-form-item label="组织机构编码：">
          <el-input v-model="editOrganFrom.idCard"></el-input>
        </el-form-item>
        <el-form-item label="简称：">
          <el-input v-model="editOrganFrom.shortName"></el-input>
        </el-form-item>
        <el-form-item label="备注：">
          <el-input type="textarea" v-model="editOrganFrom.remark"></el-input>
        </el-form-item>
        <el-form-item label="上级组织机构：">
          <el-cascader
            expand-trigger="click"
            placeholder="选择上级组织机构"
            :options="options"
            :props="optionsProps"
            change-on-select
            v-model="editOrganFrom.selectedOptions"
            @change="handleChange">
          </el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editOrgandialog = false">取 消</el-button>
        <el-button type="primary" @click="editOrganSure">保 存</el-button>
      </span>
    </el-dialog>

  </div>


</template>

<script type="text/javascript">
  import setupNav from 'components/setup/nav';
  import {organHttp} from '../../api/setupHttp.js';
  export default {
    name: 'home',
    data(){
      return {
        addOrgandialog: false,
        addOrganFrom:{
          selectedOptions:[],
          idCard: '',
          name:'',
          shortName: '',
          remark: '',
          superId:'',
          level: 0,
          orderId: 0
        },
        editOrgandialog: false,
        editOrganFrom:{
          selectedOptions:[],
          idCard: '',
          name:'',
          shortName: '',
          remark: '',
          superId:'',
          level: 0,
          orderId: 0
        },
        options: [],
        optionsProps:{
          children: 'children',
          label: 'name',
          value: 'idCard',
        },
        deptName:'',
        idCard:'',
        treeData: [],
        treeProps: {
          children: 'children',
          label: 'name'
        },
        tableData: [],
        page:{
          currentPage:1,
          sizes:[10, 20],
          size:10,
          total:0
        }
      }
    },
    created:function(){
      this.initData();
    },
    methods:{
      initData(){
        this.getList();
        this.getTree();
      },
      getTree(){
        organHttp.getTree().then((result)=>{
          this.treeData = result.data;
          this.options = result.data;
        }).catch(() => {});
      },
      getList(){
        organHttp.getList({page:this.page.currentPage,pageSize:this.page.size,deptName:this.deptName,idCard:this.idCard}).then((result)=>{
          this.tableData = result.data;
          this.page.total = result.totalRecord;
        }).catch(() => {});
      },
      searchList(){
        this.getList();
      },
      getLocalTime(row, column) {
        let date = new Date(row[column.property]);
        if (date == undefined) {
           return "";
        }
        let y = date.getFullYear();
        let m = date.getMonth() + 1;
        m = m < 10 ? ('0' + m) : m;
        let d = date.getDate();
        d = d < 10 ? ('0' + d) : d;
        let h = date.getHours();
        h = h < 10 ? ('0' + h) : h;
        let minute = date.getMinutes();
        let second = date.getSeconds();
        minute = minute < 10 ? ('0' + minute) : minute;
        second = second < 10 ? ('0' + second) : second;
        return y + '-' + m + '-' + d;
      },
      handleNodeClick(data) {
        this.idCard = data.idCard;
        this.getList();
      },
      handleSizeChange(val) {
        this.page.size = val;
        this.getList();
        // console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        this.page.currentPage = val;
        this.getList();
        // console.log(`当前页: ${val}`);
      },
      addOrgan(){
        this.addOrgandialog = true;
      },

      handleChange(value) {
        // console.log(value);
      },
      delOrgan(id){
        this.$confirm('是否删除该用户?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          organHttp.delOrgan({id:id}).then((result)=>{
            this.getList();
            this.getTree();
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
          }).catch(() => {});
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      addOrganSure(){
        if(this.editOrganFrom.selectedOptions == undefined || this.editOrganFrom.selectedOptions.length == 0){
          this.$message({
            type: 'info',
            message: '请选择上级组织机构'
          });
          return
        }
        this.addOrganFrom.level = this.addOrganFrom.selectedOptions.length + 1;
        this.addOrganFrom.superId = this.addOrganFrom.selectedOptions.slice(-1).join();
        organHttp.addOrgan({jsonStr:this.addOrganFrom}).then((result)=>{
          this.getList();
          this.getTree();
          this.addOrgandialog = false
          this.$message({
            type: 'success',
            message: '添加成功!'
          });
        }).catch(() => {});
      },
      getRole(id){
        organHttp.getOrgan({id:id}).then((result)=>{
          this.editOrgandialog = true;
          this.editOrganFrom = result.data;
        }).catch(() => {});
      },
      editOrganSure(){
        if(this.editOrganFrom.selectedOptions == undefined || this.editOrganFrom.selectedOptions.length == 0){
          this.$message({
            type: 'info',
            message: '请选择上级组织机构'
          });
          return
        }else{
          this.editOrganFrom.level = this.editOrganFrom.selectedOptions.length + 1;
          this.editOrganFrom.superId = this.editOrganFrom.selectedOptions.slice(-1).join();
        }
        organHttp.editOrgan({jsonStr:this.editOrganFrom}).then((result)=>{
          this.editOrgandialog = true;
          this.editOrganFrom = result.data;
        }).catch(() => {});
      }
    },
    components:{
      setupNav
    }
   }
</script>

<style lang="scss" scoped>
  @import "~assets/css/lib-variable";
  @import "~assets/css/lib-mixin";

  .organ-wrap{
    width:  $site-width;
    min-width: $site-minwidth;
    min-height:$site-minheight;
    padding:0 20px;
    box-sizing:border-box;
    .organ-tree{float:left;width:250px;}
    .organ-cont{margin-left:260px;}
  }

</style>



