import xhr from '../../static/api'
import xhr2 from '../../static/api2'

export const seoHttp = {
  /**
   * 重点人员添加
   * @param params
   * @returns {*|any}
     */
  addKeyPeople(params){
    return xhr.post("/keyPeople/add",params)
  },
  /**
   * 设备添加
   * @param params
   * @returns {*|any}
     */
  addEquip(params){
    return xhr.post("/equip/addEquip",params)
  },
  /**
   * 场所添加
   * @param params
   * @returns {*|any}
     */
  addPlace(params){
    return xhr.post("/place/addPlace",params)
  },


  searchMac(params) {
    return xhr.post('/syn/mac',params)
  },
  searchPlace(params) {
    return xhr.post('/syn/place',params)
  },
  searchEquipment(params) {
    return xhr.post('/syn/equip',params)
  },
  searchKeyPerson(params) {
    //return xhr.post('/keyPeople/list',params)
    return xhr.post('/syn/keyPeople',params)
  },
  serchKey(params){
    return xhr2.post('/syn/getKeyPeopleInfo',params)
  },
  macChart(params) {
    return xhr2.post('/syn/appearPlaceOrder',params)
  },
  netWorkLog(params) {
    return xhr.post('/syn/networkLog',params)
  },
  selectPlace(params) {
    return xhr2.post('/syn/getPlaceByAreaCode',params)
  },
  selectEquip(params) {
    return xhr2.post('/syn/getEquipByServiceCode',params)
  },

  //根据设备编码查找采集mac
  getMacByEquip(params) {
    return xhr2.post('/gis/getMac',params)
  },
  //根据设备编码查找采集重点人员
  getKeyPersonByEquip(params) {
    return xhr2.post('/gis/getKeyPeopleByEquipNum',params)
  }
}



