<template>
  <!--分页插件-->
  <div class="page">
    <el-pagination
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :pager-count="count"
      layout="total,prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        count:5,
        total:40,
        currentPage:1
      }
    },
    props:["totalCount","curr"],
    methods:{
      handleCurrentChange(val) {
        this.$emit('changePage',val);
      }
    },
    watch:{
      totalCount:function(val){
        this.total=this.totalCount;
      },
      curr:function(val){
        this.currentPage=val;
      }
    },
    mounted(){
      this.total=this.totalCount;
      this.currentPage=this.curr;
    }
  }
</script>
<style lang="scss" scoped>
  .page{
    clear:both;
    text-align: right;
  }
</style>



