import Vue from 'vue'
import Router from 'vue-router'
import Home from 'pages/home/home'
import clothControl from 'pages/page2/page2'
import collisionAnalysis from 'pages/page3/page3'
import dataCount from 'pages/dataCount/dataCount'

import seo from 'pages/seo/seo'
import userManage from 'pages/setup/user'
import organization from 'pages/setup/organization'
import role from 'pages/setup/role'
import loginLog from 'pages/setup/loginLog'
import operationLog from 'pages/setup/operationLog'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',  //路由默认跳转
      redirect: 'home'
    }, {
      path: '/home',
      name: 'home',
      component: Home
    },
    {
      path: '/clothControl',
      name: 'clothControl',
      component: clothControl
    },
    {
      path: '/collisionAnalysis',
      name: 'collisionAnalysis',
      component: collisionAnalysis
    },
    {
      path: '/seo',
      name: 'seo',
      component: seo
    },
    {
      path: '/dataCount',
      name: 'dataCount',
      component: dataCount
    },
    {
      path: '/userManage',
      name: 'userManage',
      component: userManage
    },
    {
      path: '/organization',
      name: 'organization',
      component: organization
    },
    {
      path: '/role',
      name: 'role',
      component: role
    },
    {
      path: '/loginLog',
      name: 'loginLog',
      component: loginLog
    },
    {
      path: '/operationLog',
      name: 'operationLog',
      component: operationLog
    }
  ],
  linkActiveClass: 'active'
})



