<template>
    <div class="chart-wrapper" ref="chart" style="height:100%;width:100%">

    </div>
</template>

<script>
  export default {
  name: 'bar',
  props: {
    echartsData: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {}
  },
  watch: {
      echartsData: {
        handler: function (val, oldVal) {
          this.initChart()
        },
        deep: true  //增加deep 观察对象的子对象变化
      }
  },
  mounted() {
    this.$nextTick(()=>{
         this.myChart = this.$echarts.init(this.$refs.chart);
      this.initChart()
    })
  },
  methods: {
   initChart() {
      let option = {
          grid: {
              left: '5%',
              right: '5%',
              top:'5%',
              bottom: '3%',
              containLabel: true
          },
          xAxis:  {
              show:false,
              type: 'value',
          },
          yAxis: {
              show:true,
              type: 'category',
              inverse: true,
              data: this.echartsData.ydata,
              nameTextStyle:{color:'#8199c3'},
              splitLine:{ show:false },
              axisTick:{ show:false },
              axisLabel:{
                  color:'#333'
              },
              axisLine:{
                  show:false,
                  lineStyle:{
                      color:"#18418a"
                  }
              }
          },
          series: [
              // {
              //     name: '访问次数',
              //     type: 'bar',
              //     barGap:'-100%',
              //     barCategoryGap: '90%',
              //     itemStyle:{
              //         normal:{
              //             color:'#f0f0f0'
              //         }
              //     },
              //     silent:true,
              //     data: this.echartsData.maxdata

              // },
              {
                name: '访问次数',
                type: 'bar',
                barCategoryGap: '90%',
                label: {
                    normal: {
                        show: true,
                        position:'right',
                        color:'#38b274',
                        fontWeight:'bold',
                        formatter: function(params){
                          return params.value + '次';
                        }
                    }
                },
                itemStyle: {
                  normal: {
                    color: new this.$echarts.graphic.LinearGradient(
                      1, 0, 0, 0, [{
                          offset: 0,
                          color: '#8ff0c8'
                        },
                        {
                          offset: 1,
                          color: '#38b274'
                        }
                      ]
                    )
                  }
                },
                silent:true,
                data: this.echartsData.data
              }

          ]
      }
      this.myChart.on('click',(params) =>{
          this.$emit('handleCharts',params.name)
      })
      this.myChart.setOption(option);

    }

  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

</style>



