import xhr from '../../static/api'
import xhr2 from '../../static/api2'

//布控管理
export const  Arrangement={
  addTask(params) {
    return xhr.post('/executecontrol/addExecutecontrol',params)
  },
  search(params){
    return xhr2.post('/executecontrol/selExecutecontrols',params)
  },
  getTaskDetial(params){
    return xhr2.post('/homepage/executecontrolList',params)
  },
  detelTask(params){
    return xhr2.post('/executecontrol/delExecutecontrol',params)
  },
  macList(params){
    return xhr2.post('/executecontrol/executecontrolList',params)
  },
  keyPeople(params){
    return xhr2.post('/executecontrol/makekeypeople',params)
  },
  searchPlace(params){
    return xhr.post('/syn/place',params)
  }
}



//   碰撞管理

export const Collision={
  search(params){
    return xhr2.post('/texecutecollide/selTinfoCollideTask',params)
  },
  taskList(params){
    return xhr2.post('/texecutecollide/getTinfoCollide',params)
  },
  taskDetial(params){
    return xhr2.post('/texecutecollide/texecutecollideTaskDetails',params)
  },
  addTask(params) {
    return xhr.post('/texecutecollide/addTexecutecollide',params)
  },
  delTask(params){
    return xhr2.post('/texecutecollide/delTexecutecollide',params)
  }
}




