<template>
  <el-dialog
    :visible.sync="macDialog"
    width="50%"
    @close="closeDialog">
    <el-tabs v-model="macDialogActive">
      <el-tab-pane label="Mac信息" name="1">
        <el-row :gutter="20">
          <el-col :span="12">
            <el-form ref="div" :model="macForm" label-width="120px">
              <el-form-item label="Mac地址：">
                {{macForm.mac}}
              </el-form-item>
              <el-form-item label="手机号：">
                {{macForm.phone}}
              </el-form-item>
              <el-form-item label="imei：">
                {{macForm.imei}}
              </el-form-item>
              <el-form-item label="imsi：">
                {{macForm.imsi}}
              </el-form-item>
              <el-form-item label="首次出现时间：">
                {{macForm.firstAppearTime}}
              </el-form-item>
              <el-form-item label="首次出现场所：">
                {{macForm.firstAppearPlace}}
              </el-form-item>
              <el-form-item label="最近出现时间：">
                {{macForm.lastAppearTime}}
              </el-form-item>
              <el-form-item label="最近出现场所：">
                {{macForm.lastAppearPlace}}
              </el-form-item>
              <el-form-item label="出现次数：">
                {{macForm.appearNum}}
              </el-form-item>
            </el-form>
          </el-col>
          <el-col :span="12">
            <div class="macChart">
              <EchartsMac :echartsData="echartsData"></EchartsMac>
            </div>
          </el-col>
        </el-row>
      </el-tab-pane>
      <el-tab-pane label="上网记录" name="2">
        <el-table
          :data="macTable"
          tooltip-effect="dark"
          style="width: 100%"
          stripe>
          <el-table-column
            label="上网场所"
            prop="serviceName"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            label="采集设备"
            show-overflow-tooltip>
            <template slot-scope="scope">
              <el-button type="text">{{scope.row.equipName}}</el-button>
            </template>
          </el-table-column>
          <el-table-column
            label="发现时间"
            prop="startTime"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            label="离开时间"
            prop="endTime"
            show-overflow-tooltip>
          </el-table-column>
        </el-table>
      </el-tab-pane>
      <el-tab-pane label="查看轨迹" name="3">
        <div id="map_lujing" v-if="macDialogActive == 3">
          <iframe :style="{'height':100+'%','width':100+'%'}"  src="../../../static/gis/xl_track.html" />
        </div>
      </el-tab-pane>
      <el-tab-pane label="APP应用" name="4"><div style="text-align:center">暂无数据</div></el-tab-pane>
    </el-tabs>
    <span slot="footer" class="dialog-footer">
        <!-- <el-button @click="macDialog = false">取 消</el-button> -->
        <el-button type="primary" @click="macDialog = false">确 定</el-button>
      </span>
  </el-dialog>
</template>
<script>
  import {Arrangement} from '../../api/collisionHttp'
  import {seoHttp} from '../../api/seoHttp.js';
  import EchartsMac from 'components/echarts/macPop'
  export default {
    name:'',
    data(){
      return{
        macDialog:false,
        macDialogActive:'1',
        macForm:{},
        macTable:[],
        echartsData:{
          ydata:[],
          // maxdata:[28,28,28,28],
          data:[]
        },
        dialogTabList: false,
        activeName: 'first',
      }
    },
    props:["outer","mac"],
    methods:{
      getCoods(coods){

      },
      handleClick(tab, event) {
        console.log(tab, event);
      },
      closeDialog(){
        this.$emit("closeDialog",false);
      },
      macDialogCk(mac){
        seoHttp.searchMac({mac:mac}).then((result)=>{
          this.macForm = result.data.features[0].properties;
          this.macDialog = true;
        }).catch(() => {});
        seoHttp.macChart({mac:mac}).then((result)=>{
          this.echartsData.data = [];
          this.echartsData.ydata = [];
          for (let item of result.data) {
            this.echartsData.data.push(item.appearNum)
            this.echartsData.ydata.push(item.serviceName)
          }
        }).catch(() => {});
        seoHttp.netWorkLog({mac:mac}).then((result)=>{
          this.macTable = result.data
        }).catch(() => {});
      },
    },
    watch:{
      outer(newValue){
        this.macDialog=newValue;
      },
      mac(newValue){
          this.macDialogCk(newValue)
      }
    },
    components:{
      EchartsMac},
    created(){
      window.vmObj = () => {
        return this
      };
    }
  }

</script>
<style lang="scss" scoped>
.macChart{width:100%;height:430px;}
#map_lujing{
  height:400px;
  }
.el-form-item{
  margin-bottom:0!important;
}
</style>



