<template>
    <div class="single-bar" ref="chart" style="height:100%;width:100%"></div>
</template>

<script>
import echarts from 'echarts'
import { DataCountHttp } from '../../api/dataCountHttp.js'
const colors = [
                ["#3c9ee8", "#1d4efbz"], ["#22d2c0", "#1ca092"], ["#1ea898", "#11635a"], ["#82ebdd", "#47bdad"], ["#8ac851", "#6cad30"],
                ["#f2d78a", "#ccab4d"], ["#fad562", "#d6af37"], ["#fc355f", "#e50f3c"], ["#fd7dd8", "#da40ae"], ["#b860ef", "#9639d1"],
                ["#825dfb", "#5d37db"], ["#5d70fb", "#3448df"], ["#5b91ea", "#346dcc"], ["#11bfff", "#05adeb"], ["#75defc", "#3db5d7"],
          ];
export default {
    props:{
        series:''
    },
    name:'SingleBar',
     data(){
      return {
          seriesData:[]
      }
    },
    watch:{
        series:{
            handler: function (val, oldVal) {
                this.initData();
            },
            deep: false 
        }
    },
    methods:{
      initData(){
          this.formaterSeries();
          this.initChartOpts();
      },
      formaterSeries(){ 
        this.seriesData = this.series.map((t, i) => {
            return {
                value: t.value,
                name: t.name,
                itemStyle: {
                color: colors[i][0]
                },
                labelLine: {
                normal: {
                    show: true,
                    lineStyle: {
                    color: '#c7c7c7'
                    }
                },
                emphasis: {
                    show: true,
                    lineStyle: {
                    color: colors[i][1]
                    }
                }
                },
                label: {
                normal: {
                    show: true,
                    color: '#818285',
                    position: 'outside',

                },
                emphasis: {
                    show: true,
                    color: colors[i][1]
                }
                }
            }
            })
      },
      initChartOpts(){
          this.myChart = echarts.init(this.$refs.chart);
          let option = {
              tooltip: {
                 trigger: 'item',
              },
              series: [
                {
                    name: '设备',
                    type: 'pie',
                    radius: ['50%', '70%'],
                    avoidLabelOverlap: true,
                    data: this.seriesData
                }
              ]
          } 
       this.myChart.setOption(option)
      }
    },
    mounted(){
        this.initData();
        
    }
}
</script>

<style lang="scss" scoped>
</style>



