import xhr from '../../static/api'
import xhr2 from '../../static/api2'

// 用户管理
export const userHttp = {
  getList(params) {
    return xhr.post('/user/listUser',params)
  },
  addUser(params) {
    return xhr.get('/user/addUser',{params})
  },
  editUser(params) {
    return xhr.get('/user/updateUser',{params})
  },
  getUser(params) {
    return xhr2.post('/user/getUser',params)
  },
  delUser(params) {
    return xhr2.get('/user/delUser',{params})
  },
  getTree(params) {
    return xhr.get('/dept/getDeptTree',{params})
  },
  getRole(params) {
    return xhr.get('/role/getAllRole',{params})
  },
  alRole(params) {
    return xhr.post('/user/authorize',params)
  },
  getRoleName(params) {
    return xhr2.post('/role/getRoleName',params)
  }
};

// 组织管理
export const organHttp = {
  getList(params) {
    return xhr.post('/dept/list',params)
  },
  addOrgan(params) {
    return xhr.get('/dept/addDept',{params})
  },
  delOrgan(params) {
    return xhr2.get('/dept/deleteDept',{params})
  },
  getOrgan(params) {
    return xhr2.post('/dept/selDeptByIdCard',params)
  },
  editOrgan(params) {
    return xhr.get('/dept/updateDept',{params})
  },
  getTree(params) {
    return xhr.get('/dept/getDeptTree',{params})
  }
};

//角色管理
export const roleHttp = {
  getList(params) {
    return xhr.post('/role/listRole',params)
  },
  addRole(params) {
    return xhr.post('/role/addRole',params)
  },
  delRole(params) {
    return xhr2.post('/role/delRole',params)
  },
  getRole(params) {
    return xhr2.post('/role/selRoleById',params)
  },
  editRole(params) {
    return xhr.post('/role/updRole',params)
  },
  getRoleCheck(params) {
    return xhr.get('/permission/list',{params})
  },
};

//登陆日志
export const llogHttp = {
  getList(params) {
    return xhr.get('/user/getAccessLog',{params})
  }
};

//操作日志
export const ologHttp = {
  getList(params) {
    return xhr.get('/systemOperationLog/list',{params})
  }
};



