<template>
    <div class="line-chart" ref="chart" style="height:100%;width:100%"></div>
</template>

<script>
import echarts from 'echarts'
import {homeHttp} from '../../api/homeHttp.js';
export default {
    name:'LineChart',
     data(){
      return {
          seriesName:[],
          seriesData:[]
      }
    },
    methods:{
      initData(){
        this.getBkList();
      },
      getBkList(){
        homeHttp.keyPeopleCount().then((result)=>{
            this.seriesName = result.data.historyWeekCount.map((t,i)=>{
                return t.weeks
            })
            this.seriesData = result.data.historyWeekCount.map((t,i)=>{
                return t.count
            })
            this.initChartOpts()
        }).catch(() => {});
      },
      initChartOpts(){
        this.myChart = echarts.init(this.$refs.chart);
            let option = {
                title: {
                    text: '重点人活跃趋势',
                    subtext: '单位：次',
                    textStyle: {
                        fontSize: 16,
                        color: '#444b53'
                    },
                    left:20,
                    top:10,
                    subtextStyle: {
                        color: '#2d3640',
                        fontSize: 12,
                    }
                },
                tooltip: {
                    trigger: 'axis',
                    formatter: '{b}<br />数据量：{c}'
                },
                grid: {
                    top:'26%',
                    left: '6%',
                    right: '6%',
                    bottom: '6%',
                    containLabel: true
                },
                xAxis: {
                    type: 'category',
                    boundaryGap: true,
                    axisLine: {
                    show: false
                    },
                    axisTick: {
                    show: false
                    },
                    data: this.seriesName
                },
                yAxis: {
                    type: 'value',
                    axisLine: {
                        show: false
                    },
                    axisTick: {
                        show: false
                    }
                },
                series: [
                    {
                    name: '单位',
                    type: 'line',
                    smooth: false,
                    symbolSize: 7,
                    symbol: 'circle',
                    itemStyle: {
                        normal: {
                        color: "#2fd1c6",  // 会设置点和线的颜色，所以需要下面定制 line
                        borderColor: "#fff"  // 点边线的颜色
                        }
                    },
                    lineStyle: {//折线的颜色
                        normal: {
                        color: "#1ba0fc",
                        width: 3,
                        //shadowBlur:80
                        },
                    },
                    data: this.seriesData,
                    areaStyle: {
                        normal: {
                        color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                            offset: 0,
                            color: 'rgb(47, 209, 198)'
                        }, {
                            offset: 1,
                            color: 'rgb(47, 209, 198,0.3)'
                        }])
                        }
                        }
                    }
                ]
            }
        this.myChart.setOption(option)
      }
    },
    mounted(){
        this.initData()
    }
}
</script>

<style lang="scss" scoped>
</style>



