<template>
  <div class="datac-wrap">
      <el-tabs v-model="activeName" @tab-click="handleClick">
          <el-tab-pane label="组数量" name="first"></el-tab-pane>
          <el-tab-pane label="今日新增" name="second"></el-tab-pane>
          <el-tab-pane label="七日新增" name="third"></el-tab-pane>
          <el-tab-pane label="月新增" name="fourth"></el-tab-pane>
      </el-tabs>
      <div class="tabs__content">
          <ul class="cards" v-if="dataStatistics2.length !== 0">
              <li v-for="item in dataStatistics2">
                  <h4>{{item.name}}</h4>
                  <div><i class="text">{{item.value}}</i></div>
              </li>
          </ul>
          <div class="radios">
              <el-radio v-model="radio" label="1">MAC</el-radio>
              <el-radio v-model="radio" label="2">手机</el-radio>
              <el-radio v-model="radio" label="3">IMEI</el-radio>
              <el-radio v-model="radio" label="4">IMSI</el-radio>
              <el-radio v-model="radio" label="5">设备数</el-radio>
              <el-radio v-model="radio" label="6">设备在线率</el-radio>
          </div>
          <div class="echart1">
              <LargeBar ref="largeBar" :series='LargeBarData'></LargeBar>
          </div>
          <div class="clearFix">
              <div class="echart2">
                  <TransverseBar  :series='transverseBar'></TransverseBar>
              </div>
              <div class="echart3">
                  <PieChart ref="child" :series='groupsCount'></PieChart>
              </div>
          </div>
      </div>
  </div>
</template>

<script type="text/javascript">
import { DataCountHttp } from '../../api/dataCountHttp.js'
import LargeBar from '../../components/echarts/largeBar'
import PieChart from '../../components/echarts/pieChart'
import TransverseBar from '../../components/echarts/transverseBar'
export default {
  name: 'dataCount',
  data() {
    return {
      activeName: 'first',
      radio: '1',
      groupsCount: [],
      LargeBarData: [],
      dataStatistics: [],
      transverseBar: [],
      dataStatistics2: [],
    }
  },
  watch: {
    radio: {
      handler: function(val, oldVal) {
        let params = {
          type: val
        }
        this.loadGroupsRise(params)
      },
      deep: true
    },
    dataStatistics:{
      handler: function(val, oldVal) {
        this.dataStatistics2 = this.dataStatistics.map((t,i)=>{
          switch(t.name){
            case 'id':
                t.name = '特征数'
                break;
            case 'mac':
                t.name = 'MAC'
                break;
            case 'phone':
                t.name = '手机'
                break;
            case 'imei':
                t.name = 'IMEI'
                break;
            case 'imsi':
                t.name = 'IMSI'
                break;
            case 'servicecode':
                t.name = '场所'
                break;
            case 'devicenum':
                t.name = '设备'
                break;
            case 'key_people':
                t.name = '重点人数'
                break;
            case 'qq':
                t.name = 'QQ'
                break;
            case 'wx':
                t.name = '微信'
                break;
          }
          return {
            name:t.name,
            value:t.value
          }
        })
      },
      deep: true
    }
  },
  methods: {
    loadGroupsRise(params) {
      DataCountHttp.asDStatistics({ params })
        .then(result => {
          this.groupsCount = result.data.equip
          this.LargeBarData = result.data.area
          this.transverseBar = result.data.place
        })
        .catch(() => {})
    },
    handleClick(tab, event) {
      let type = this.activeName;
      switch(type){
        case 'first':
          type = 1
          break;
        case 'second':
          type = 2
          break;
        case 'third':
          type = 3
          break;
        case 'fourth':
          type = 4
          break;
      }
      let params = {
        type:type
      }
      this.loadDataStatistics(params)
      /*document.getElementById("pane-second").style.display = 'none';
            document.getElementById("pane-third").style.display = 'none';
            document.getElementById("pane-fourth").style.display = 'none';
            document.getElementById("pane-first").style.display = 'block';*/
    },
    loadDataStatistics(params) {
      DataCountHttp.groupsRise({params})
        .then(result => {
          this.dataStatistics = result.data;
        })
        .catch(() => {})
    }
  },
  mounted: function() {
    let params = {
      type: 1
    }
    this.loadGroupsRise(params)
    this.loadDataStatistics(params)
  },
  components: {
    LargeBar,
    PieChart,
    TransverseBar
  }
}
</script>

<style lang="scss" scoped>
@import '~assets/css/lib-variable';
@import '~assets/css/lib-mixin';

* {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.datac-wrap {
  /*width:  $site-width;*/
  min-width: $site-minwidth;
  min-height: $site-minheight;
  border: 1px solid #e4e7ed;
  position: relative;
  box-sizing: border-box;
  margin: 20px;
  background-color: #fff;
  color: #000;
}
.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  li {
    text-align: center;
    margin-bottom: 15px;
    margin-right: 34px;
    width: 200px;
    height: 112px;
    border: 1px solid;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
    h4 {
      line-height: 52px;
      font-size: 18px;
      color: #fff;
    }
    div {
      font-size: 18px;
      height: 60px;
      display: flex;
      justify-content: center;
      color: #000;
      align-items: center;
      .text {
        height: 27px;
      }
    }
  }
  li:nth-child(1) {
    border-color: #ff6262;
    h4 {
      background-color: #ff6262;
      color: #fff;
    }
    div {
      color: #ff6262;
    }
  }
  li:nth-child(2) {
    border-color: #f5c615;
    h4 {
      background-color: #f5c615;
    }
    div {
      color: #f5c615;
    }
  }
  li:nth-child(3) {
    border-color: #068dac;
    h4 {
      background-color: #068dac;
    }
    div {
      color: #068dac;
    }
  }
  li:nth-child(4) {
    border-color: #0aa796;
    h4 {
      background-color: #0aa796;
    }
    div {
      color: #0aa796;
    }
  }
  li:nth-child(5) {
    border-color: #1579cd;
    h4 {
      background-color: #1579cd;
    }
    div {
      color: #1579cd;
    }
  }
  li:nth-child(6) {
    border-color: #0cabe2;
    h4 {
      background-color: #0cabe2;
    }
    div {
      color: #0cabe2;
    }
  }
  li:nth-child(7) {
    border-color: #6476d7;
    h4 {
      background-color: #6476d7;
    }
    div {
      color: #6476d7;
    }
  }
  li:nth-child(8) {
    border-color: #4d8abe;
    h4 {
      background-color: #4d8abe;
    }
    div {
      color: #4d8abe;
    }
  }
  li:nth-child(9) {
    border-color: #4d8abe;
    h4 {
      background-color: #4d8abe;
    }
    div {
      color: #4d8abe;
    }
  }
  li:nth-child(10) {
    margin-right: 0;
    border-color: #4d8abe;
    h4 {
      background-color: #4d8abe;
    }
    div {
      color: #4d8abe;
    }
  }
}
.set-out {
  width: 108px;
  height: 40px;
  border: 5px solid #d1e5f6;
  position: absolute;
  top: 2px;
  right: 5px;
  border-radius: 20px;
  background: radial-gradient(#339aea, #45b7f8);
  font-size: 14px;
  color: #fff;
  cursor: pointer;
  z-index: 1;
  font-weight: 700;
}
.clearFix {
  height: 370px;
  clear: both;
}
.echart1 {
  height: 260px;
  // border: 1px solid #e4e7ed;
  margin-bottom: 15px;
}
.echart2 {
  display: inline-block;
  width: 50%;
  height: 370px;
  float: left;
}
.echart3 {
  height: 370px;
  width: 50%;
  display: inline-block;
  float: left;
}

.tabs__content {
  padding: 15px;
}
</style>
<style>
.datac-wrap .el-tabs__header {
  /*border-left: 1px solid #e4e7ed;
        border-right: 1px solid #e4e7ed;
        border-top: 1px solid #e4e7ed;*/
  padding: 5px 0 0;
  margin: 0;
}
.datac-wrap .el-tabs__nav-wrap::after {
  height: 1px;
}
.datac-wrap .el-tabs__active-bar {
  left: 20px;
}
.datac-wrap #tab-first {
  margin-left: 20px;
}
.radios {
  font-size: 14px;
  color: #333;
  line-height: 26px;
  padding: 10px;
}
.radios .el-radio__inner::after {
  width: 9px;
  height: 9px;
  border-radius: 100%;
  background: radial-gradient(#277aff, #3991ff, #409eff); /* 标准的语法 */
  /*background-color: #409EFF;*/
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%) scale(0);
  transform: translate(-50%, -50%) scale(0);
  -webkit-transition: -webkit-transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6);
  transition: -webkit-transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6);
  transition: transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6);
  transition: transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6),
    -webkit-transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6);
  transition: transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6),
    -webkit-transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6);
}
.radios .el-radio__input.is-checked .el-radio__inner {
  border-color: #dcdfe6;
  background: #fff;
}

.radios .el-radio__inner {
  border: 1px solid #dcdfe6;
  border-radius: 100%;
  width: 16px;
  height: 16px;
  background-color: #fff;
  cursor: pointer;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
</style>



