                <td v-if="item.serviceName.length>0">{{item.serviceName.join(',')}}</td>
                <td v-else></td>
              </tr>
              <tr>
              <td>发现目标：</td>
              <td>{{item.serviceName.length}}人</td>
              </tr>
            </table>
