</template>
<script type="text/javascript">
  import pageCtrl from '../../components/pagingctrl.vue'
  import {Arrangement,Collision} from '../../api/collisionHttp'
  import {seoHttp} from '../../api/seoHttp.js';
  import EchartsMac from 'components/echarts/macPop'
  import TabList from '../components/tabList.vue'
  import TaskDetial from '../components/taskDetial.vue'
  export default {
    name: 'page2',
    data() {
      return {
        currentTaskId:'',                          //  当前选中的任务id
        macDialogMac:'',                        // mac详情--当前选框中的mac
        pageSwjl:{
          currentPage:1,
          sizes:[10, 20],
          size:10,
          total:0
        },
        pageSwj2:{
          currentPage:1,
          size:10,
          total:0
        },
        pageSwj3:{
          currentPage:1,
          size:10,
          total:0
        },
        pickerOptions1:{
          disabledDate(time){
            return time.getTime() > Date.now();
          }
        },
        detialData:{},     //任务详情--数据集合
        resArr:[],                                                                   //任务详情--区域数据集合
        resDetial:[],
        coods:[],
        tabActiveName: "0",                                                            //任务详情--默认选中选项卡
        currentTaskIndex:0,                                                              //任务详情--当前任务下
        currentAreaMes:[],
        macDialogActive:'1',                                   //mac对应的详细信息-tab 默认选中的选项卡
        macForm:{},                                            //mac对应的详细信息-mac信息选项卡
        macTable:[],                                            //mac对应的详细信息-上网信息选项卡
        echartsData:{
          ydata:[],
          // maxdata:[28,28,28,28],
          data:[]
        },                                    //mac对应的详细信息-eachart图对应的数据
        dialogTabList: false,                                   //mac对应的详细信息--dialog显示和隐藏
        tabPosition: 'left',                                      // 碰撞新增--tab样式控制
        dialogAddTask: false,                                     //碰撞新增--dialog显示和隐藏
        activeAddName: '1',                                     // 碰撞新增- tab默认选项卡
        currentTab:0,                                           //  碰撞新增--tab 当前选项卡的下标
        lastCodes:[],                                     //  任务添加-当前所在区域选中的内容
        typeList:[{value:0,label:'全部'},{value:1,label:'MAC'},{value:2,label:'手机号'},{value:3,label:'IMEI'},{value:4,label:'IMSI'}],
        taskContent: {                                          //碰撞新增--用户数的数据
          taskName: '',
          targetType:0,
        },
        timeInterval:[{value:1,label:'全天'},{value:2,label:'夜晚'},{value:3,label:'白天'},{value:4,label:'自定义'}],    //  对应的时间段
        editableTabsValue: '0',                 // 任务添加-任务区域tab默认选中的选项卡
        tabIndex: 0,
        editableTabs: [
         {                                          //  碰撞新增--区域信息数据
          title: '区域1',
          name: '0',
          content: {
            beginDate:"",
            endDate:"",
            startTime: "00:00",
            endTime: "24:00",
            sel:1,
            count:'',
            serviceCodes:'',
            serviceName:''
          }
        }],
        currentMac:'',
        timeFrame:[{value:1,label:'全天'},{value:2,label:'夜晚'},{value:3,label:'白天'},{value:4,label:'自定义'}],
        inputTaskName:'',                                       //  碰撞管理--用户输入的任务名称
        dialogTaskDetial:false,                                // 任务详情---dialog的显示和隐藏
        gridData:[],                                          // 碰撞管理---碰撞列表数据
        record:{
          currentPage:1,
          pageSize:10,
          total:1,
        },
        currentPage:1,
        pageSize:10,
        total:5,
        targetValue:0,                                          // 碰撞管理---搜索功能碰撞类型
        targetList:[{status:0,label:'全部'},{status:1,label:'运行中'},{status:2,label:'已结束'}],
        tasklistData:[],
      };
    },
    methods:{
      areaClick(tab, event) {
        this.currentTab=tab.index;
        let ifr3=document.querySelector("#map_add_view2").contentWindow;
        ifr3.location.reload();
        let str=this.editableTabs[this.currentTab].content.serviceCodes;
        let len=this.editableTabs[this.currentTab].content.serviceCodes.length;
        let str1=str.substr(0,-1+len);
        if(str1!==''){
          str1=str1.split(",");
          this.lastCodes=str1;
        }
      },
      add_task(){
        this.dialogAddTask=true;
        this.taskContent={                                          //碰撞新增--用户数的数据
          taskName: '',
          targetType:0,
        };
        this.editableTabs=[
          {                                          //  碰撞新增--区域信息数据
            title: '区域1',
            name: '0',
            content: {
              beginDate:"",
              endDate:"",
              startTime: "00:00",
              endTime: "24:00",
              sel:1,
              count:'',
              serviceCodes:'',
              serviceName:''
            }
          }];
        setTimeout(function(){
          let ifr3=document.querySelector("#map_add_view2").contentWindow;
          ifr3.location.reload()
        },2000)
      },
      getDateByDay(time) {
        let oDate = new Date(time);
        return '' + oDate.getFullYear() + '-' + toTen(oDate.getMonth() + 1) + '-' + toTen(oDate.getDate());
        function toTen(number) {
          return parseInt(number) < 10 ? '0' + number : '' + number;
        }
      },
      getAreaData(ind){
        let serviceCodes=this.resArr[ind].serviceCodes;     // 每个区域的场所code的集合
        let prototypes=Object.keys(this.coods);   //  保存所有code对应的经纬度详细信息
        this.currentAreaMes=[];
        let that=this;
        prototypes.forEach(function(val1){
          serviceCodes.forEach(function(val2){
            if(val1==val2){
              that.currentAreaMes.push(that.coods[val1][0])
            }
          })
        })
        if(ind==0){
          setTimeout(function(){
            const ifr1=document.querySelector("#map_view_detial2").contentWindow;
            ifr1.resetData();},2000)

        }else{
          const ifr1=document.querySelector("#map_view_detial2").contentWindow;
          ifr1.resetData();
        }

      },
      detialTab(tab, event) {
        this.getAreaData(tab.index);
      },
      delTask(){
          //碰撞
          Collision.delTask({id:this.currentTaskIndex}).then((res)=>{
            if(res.code==200){
              this.$message({
                message: '删除成功',
                type: 'success'
              });
            }
            this.dialogTaskDetial=false;
            this.getData({
              pageNum:this.currentPage,
              pageSize:this.pageSize
            });
          }).catch((res)=>{})
      },
      macDialogCk(mac){
        seoHttp.searchMac({mac:mac}).then((result)=>{
          this.macForm = result.data.features[0].properties;
          this.macDialog = true;
        }).catch(() => {});
        seoHttp.macChart({mac:mac}).then((result)=>{
          this.echartsData.data = [];
          this.echartsData.ydata = [];
          for (let item of result.data) {
            this.echartsData.data.push(item.appearNum)
            this.echartsData.ydata.push(item.serviceName)
          }
        }).catch(() => {});
        this.macDialogMac = mac;
        let params={mac:this.macDialogMac,page:this.pageSwjl.currentPage,pageSize:this.pageSwjl.size}
        this.netWorkLog(params);
      },
      netWorkLog(params){
//        this.pageSwjl.currentPage = 1;
        seoHttp.netWorkLog(params).then((result)=>{
          this.macTable = result.data;
          this.pageSwjl.total=result.totalRecord;
          this.pageSwj2.total = Math.ceil(result.totalRecord/this.pageSwjl.size);
        }).catch(() => {});
      },
      handleSizeChangeSwjl(val) {
        this.pageSwjl.size = val;
        this.netWorkLog({mac:this.macDialogMac,page:this.pageSwjl.currentPage,pageSize:this.pageSwjl.size});
      },
      handleCurrentChangeSwjl(val) {
        this.pageSwjl.currentPage = val;
        this.netWorkLog({mac:this.macDialogMac,page:this.pageSwjl.currentPage,pageSize:this.pageSwjl.size});
      },
      handleCurrentChangeSwjl2(val) {
        this.pageSwj2.currentPage = val;
        this.netWorkLog({mac:this.macDialogMac,page:this.pageSwj2.currentPage,pageSize:this.pageSwj1.size});
      },
      handleCurrentChangeSwjl3(val){
        this.pageSwj3.currentPage = val;
        this.getGridData(this.currentTaskId)
      },
      getCoods(name,code,status){
        let serviceName=this.editableTabs[this.currentTab].content.serviceName;
        let serviceCodes=this.editableTabs[this.currentTab].content.serviceCodes;
        let baseNames,baseCode;
        let res=new RegExp(name,"g");
        let resCode=new RegExp(code,"g");
        if(status==0){
          //  取消
          let res=new RegExp(name+',',"g");
          let resCode=new RegExp(code+',',"g");
          baseNames=serviceName.replace(res,'');
          baseCode=serviceCodes.replace(resCode,'');
        }else{
          //  选中
          if(!res.exec(serviceName)){
            //   去重
            baseNames=serviceName+name+',';
            baseCode=serviceCodes+code+',';
          }
        }
        this.editableTabs[this.currentTab].content.serviceName=baseNames;
        this.editableTabs[this.currentTab].content.serviceCodes=baseCode;

      },
      handleTabsEdit(targetName, action) {
        if (action === 'add') {
          let len=this.editableTabs.length;
          if(len>=3){
            this.$message({
              message: '区域最多添加3个',
              type: 'warning'
            });
            return ;
          }
          let ifr3=document.querySelector("#map_add_view2").contentWindow;
          ifr3.location.reload();
          let newTabName = ++this.tabIndex + '';
          console.log(newTabName)
          this.editableTabs.push({
            title:  '区域' + (this.editableTabs.length + 1),
            name: newTabName ,
            content: {
              startTime: "00:00",
              endTime: "24:00",
              sel:1,
              count:'',
              serviceCodes:'',
              serviceName:''
            }
          });
          this.editableTabsValue = newTabName;
          this.currentTab=newTabName;
          let str=this.editableTabs[this.currentTab].content.serviceCodes;
          let str1=str.substr(0,-1+len);
          if(str1!==''){
            str1=str1.split(",");
            this.lastCodes=str1;
          }
        }
        if (action === 'remove') {
          let tabs = this.editableTabs;
          let activeName = this.editableTabsValue;
          if (activeName === targetName) {
            tabs.forEach((tab, index) => {
              if (tab.name === targetName) {
                let nextTab = tabs[index + 1] || tabs[index - 1];
                if (nextTab) {
                  activeName = nextTab.name;
                }
              }
            });
          }
          --this.tabIndex;
          this.editableTabsValue = activeName;
          this.currentTab=activeName;
          this.editableTabs = tabs.filter(tab => tab.name !== targetName);
        }
      },
      addTask() {
        this.taskContent.addTask.push({inputTarget: ''})
      },
      place_search() {
      },
      start_collision() {
        //   基本信息的参数
        let params = this.taskContent;
        if(!params.taskName) {
          this.$message({
            message: '请输入任务名称',
            type: 'warning'
          });
          return ;
        }
        let areas = [];
        if (params.targetType == 0) {
          this.$message({
            message: '请选择碰撞类型',
            type: 'warning'
          });
          return;
        }
        //   区域信息数据
        for (let item of this.editableTabs) {
          if (!(item.content.beginDate && item.content.endDate)) {
            this.$message({
              message: '请输入时间范畴',
              type: 'warning'
            });
            return;
          }
          if (!(item.content.count)) {
            this.$message({
              message: '请输入出现次数',
              type: 'warning'
            });
            return;
          }

            if (item.content.serviceCodes != '') {
              item.content.serviceCodes = item.content.serviceCodes.split(",");
              item.content.serviceCodes.forEach(function (val, ind) {
                if (val == '') {
                  item.content.serviceCodes.splice(ind, 1)
                }
              })
              areas.push(item.content)
            } else {
              item.content.serviceCodes = [];
              areas.push(item.content)
            }
          params.executecollideWayId = JSON.stringify(areas);
          Collision.addTask(params).then(res => {
            this.dialogAddTask = false;
            this.getData({
              pageNum: this.currentPage,
              pageSize: this.pageSize
            });
          }).catch(res => {
          })
        }
      },

      changeKey(){
        //    列为重点
        let arr=[];
        for(let item of this.gridData){
          if(item.keyPeople==true){
            arr.push(item.mac)
          }
        }
        Arrangement.keyPeople({
          macs:arr.join(','),
          type:2
        }).then(res=>{
          if(res.code==200){
            this.$message({
              message: '列为重点成功',
              type: 'success'
            });
          }
        }).catch(res=>{})
      },
      closeTaskDetail(){
        this.dialogTaskDetial=false;
        this.getData({
          pageNum:this.currentPage,
          pageSize:this.pageSize
        });
      },
      tabClick(mac){
        this.dialogTabList=true;
        this.macDialogCk(mac)
      },
      showTaskdetial(item){
        this.currentTaskIndex=item.id;
        this.dialogTaskDetial=true;
        this.getTaskDeial(item.id)
      },
      getTaskDeial(id){
        Collision.taskDetial({id:id}).then((result)=>{
          this.detialData=result.data[0];
          this.coods=this.detialData.result;
          let obj=JSON.parse(this.detialData.executecontrol_way_id);
          if(this.detialData.task_begin_time){
            this.detialData.task_begin_time=this.getDateByDay(this.detialData.task_begin_time);
          }
          if(this.detialData.task_end_time){
            this.detialData.task_end_time=this.getDateByDay(this.detialData.task_end_time);
          }
          if(this.detialData.create_time){
            this.detialData.create_time=this.getDateByDay(this.detialData.create_time);
          }
          obj.forEach(function(val,index){
            val.name=index.toString();
            val.label="区域"+(index+1);
          })
          this.resArr=obj;
          this.getAreaData(0);
        }).catch(() => {});
      },
      getDateByDay(time) {
        //  转化时间格式
        let oDate = new Date(time);
        return '' + oDate.getFullYear() + '-' + toTen(oDate.getMonth() + 1) + '-' + toTen(oDate.getDate());

        function toTen(number) {
          return parseInt(number) < 10 ? '0' + number : '' + number;
        }
      },
      showTaskList(id){
        this.pageSwj3.currentPage = 1//点击任务时初始化详情列表页码
        this.gridData=[];
        this.currentTaskId=id;
        this.getGridData(id)
      },
      getGridData(id){
        Collision.taskList({id:id,pageNum:this.pageSwj3.currentPage,pageSize:this.pageSwj3.size}).then(res=>{
            for(let item of res.data){
              item.maxTime=this.getDateByDay(item.maxTime);
              item.minTime=this.getDateByDay((item.minTime)*1000)
            }
            this.gridData=res.data;
            this.pageSwj3.total=res.totalRecord
        }).catch(res=>{})
      },
      getData(params){
        Collision.search(params).then((res)=>{
          this.tasklistData=res.data;
          this.total=res.data.length;
          this.getGridData(this.tasklistData[0].id)
        }).catch(()=>{})
      },
      recordPage(val){
        this.record.currentPage=val;
      },
      changePage(val){
        this.currentPage=val;
        this.getData({
          pageNum:val,
          pageSize:this.pageSize
        });
      },
      search(){
        let params={
          taskName:this.inputTaskName,
          pageNum:this.currentPage,
          pageSize:this.pageSize,
        };
        if(this.targetValue!=0){
          params.status=this.targetValue;
        }
        this.getData(params)
      },
      choseAddDialog(){
        this.dialogAddTask=false;
        this.getData({
          pageNum:this.currentPage,
          pageSize:this.pageSize
        });
      }
    },
    watch:{
      'editableTabs':{
        handler:function(val,oldVal){
          let itemData=val[this.currentTab].content;
          switch(itemData.sel)
          {
            case 1:
              itemData.startTime="00:00:00";
              itemData.endTime="24:00:00";
              break;
            case 2:
              itemData.startTime="20:00:00";
              itemData.endTime="06:00:00";
              break;
            case 3:
              itemData.startTime="05:00:00";
              itemData.endTime="19:00:00";
              break;
            case 4:
              itemData.startTime="";
              itemData.endTime="";
              break;
          }
        },
        // 深度观察
        deep:true
      },
    },
    mounted(){
      window.vmObj = () => {
        return this
      };
      this.getData({
        pageNum:this.currentPage,
        pageSize:this.pageSize
      });
    },
    components:{
      pageCtrl,
      TabList,
      TaskDetial,
      EchartsMac
    }
  }
</script>
<style>
  .el-dialog__body{
    overflow: hidden;
  }
</style>
<style lang="scss" scoped>
  .task-detial{
    .dialog-left{
      width:400px;
      float:left;
    }
    .dialog-right{
      height:400px;
      overflow: hidden;
    }
    .opertaion{
      text-align: right;
      font-size:14px;
      font-weight: 600;
      cursor: pointer;
    }
    .opertaion:hover{
      color:#409EFF;
    }
    .table{
      width:450px;
      tr{
        height:30px;
      }
      td:first-child{
        width:100px;
        text-align: right;
      }
    }
  }
  .page2-wrap{
    margin:0  auto;
    padding:20px;
    height:750px;
    color:#000;
    background: #fff;
  }
  .dialog-add{
    display:flex;
    display:-webkit-flex;
    .dialog-right{
      width:500px;
      .bk-start{
        padding-top:20px;
        text-align: center;
        button{
          padding:8px 28px;
          background: #67C23A;
          color:#fff;
          border-radius: 5px;
          outline: none;
        }
      }
      .line{
        text-align: center;
      }
      .area-top{
        padding-top:20px;
      }
      .area-main{
        background: #eee;
        padding:10px;
      }
    }
    .dialog-left{
      min-height:350px;
      flex:1;
      margin:0 10px;
    }
  }
  .page2-search{
    height:80px;
    line-height: 80px;
    padding:0 40px;
    background: #409EFF;
    .pg2-left{
      float:left;
    }
    .pg2-right{
      float:right;
    }
    .pg2-input{
      width:350px;
    }

  }
  .pg2-main{
    .pg2-edit{
      margin: 20px;
      font-weight: 700;
      font-size:15px;
      cursor: pointer;
      color:#0083CC;
      span:hover{
        color:#409EFF;
      }
    }
    table{
      width:400px;
      table-layout: fixed;
      text-align: center;
      thead{
        background-color: #409EFF;
      }
      th{
        font-weight: 600;
        text-align: center;
      }
      tr,th{
        height:60px;
      }
      tbody{
        .task_name{
          display: inline-block;
          width:130px;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
        tr{
          cursor: pointer;
        }
        tr:hover{
        background: #ccc;
        }
      }
    }
  }
  .macChart{width:100%;height:300px;}
  .dia-operation{
    margin:10px;
  }
  .collisionDetial{
    position: absolute;
    left:450px;
    top:180px;
    background: #eee;
  }
  #map_lujing{
    height:400px;
  }
  .el-form-item{
    margin-bottom:0!important;
  }
</style>



