        dialogMacDetial: false,                         //mac列表-- mac详情显示或隐藏标志
        gridData:[],                                   // mac列表--mac列表数据
        dialogTableVisible: false,                     // mac列表---全部内容展示或隐藏标志
        macListCoods:'',                               //mac列表---整个地图gis需要的geojson格式数据
        detialData:{task_name:'',target_type:'',task_begin_time:'',task_end_time:''},      //  任务详情数据
        resArr:[],                            //  任务详情-区域数据
        resDetial:[],                        //  任务详情-gid需要的geoJson数据
        coods:[],                             //任务详情-后台返回的场所code及坐标的数据集合
        tabActiveName: "0",                   //  任务详情-哪个区域显示标志
        placeCode:'',                          //任务详情-用户输入的场所的code或名称
        currentAreaMes:[],                       //任务详情-当前区域的经纬度信息数据集合
        defaultMac:'',
        transitionShow:false,                  //  mac列表的显示或隐藏标志
        dialogAddTask:false,                    // 添加任务dialog-显示或隐藏标志
        dialogTaskDetial:false,                 //任务详情dialog-显示或隐藏标志
        taskName:'',                            // 布控管理-用户输入的任务名称
        currentPage:1,
        total:1,
        pageSize:10,
        targetList:[{status:0,label:'全部'},{status:1,label:'未开始'},{status:3,label:'发现目标'},{status:2,label:'布控中'},{status:4,label:'已结束'}],  //布控管理-搜素中 布控类型数据集合
        taskList:[],                                       //   布控管理-任务列表数据

        tabPosition: 'left',                               // 任务添加-tab显示方式
        activeName: 'first',                              // 任务添加-tab默认显示哪个选项卡
        currentTab:0,                                     //任务添加-选中选项卡的下标
        lastCodes:[],                                     //  任务添加-当前所在区域选中的内容
        timeFrame:[{value:1,label:'全天'},{value:2,label:'夜晚'},{value:3,label:'白天'},{value:4,label:'自定义'}],   //任务添加-选中的时间段对应的值
        baseData: {                                       // 任务添加-基本信息的对象
          taskName: '',
          typeList:[{value:0,label:'全部'},{value:1,label:'MAC'},{value:2,label:'手机号'},{value:3,label:'IMEI'},{value:4,label:'IMSI'}],
          targetType:0,
          taskEndTimeStr:'',
          taskBeginTimeStr:'',
          addTask:[{inputTarget:''}],
        },
        editableTabsValue: '0',                 // 任务添加-任务区域tab默认选中的选项卡
        tabIndex: 0,
        editableTabs: [{                         // 任务添加-任务区域数据
          title: '区域1',
          name: '0',
          content: {
            startTime: "00:00",
            endTime: "24:00",
            sel:1,
            count:'',
            serviceCodes:'',
            serviceName:''
          }
        }],
        macDialogMac:'',                        // mac详情--当前选框中的mac
      };
    },
    methods:{
      selMap(data) {
        var ifr2 = document.querySelector("#map_view_main").contentWindow;
        ifr2.listLocation(data);
      },
      add_task(){
         this.dialogAddTask=true;
         this.baseData= {
           taskName: '',
             typeList:[{value:0,label:'全部'},{value:1,label:'MAC'},{value:2,label:'手机号'},{value:3,label:'IMEI'},{value:4,label:'IMSI'}],
              targetType:0,
             taskEndTimeStr:'',
             taskBeginTimeStr:'',
             addTask:[{inputTarget:''}],
         };
         this.editableTabs= [{                         // 任务添加-任务区域数据
           title: '区域1',
           name: '0',
           content: {
             startTime: "00:00",
             endTime: "24:00",
             sel:1,
             count:'',
             serviceCodes:'',
             serviceName:''
           }
         }],
         setTimeout(function(){
           let ifr3=document.querySelector("#map_add_view").contentWindow;
           ifr3.location.reload()
         },2000)
       },
      macDialogCk(mac){
        this.dialogMacDetial = true;
        seoHttp.searchMac({mac:mac}).then((result)=>{
          this.macForm = result.data.features[0].properties;
        }).catch(() => {});
        seoHttp.macChart({mac:mac}).then((result)=>{
          this.echartsData.data = [];
          this.echartsData.ydata = [];
          for (let item of result.data) {
            this.echartsData.data.push(item.appearNum)
            this.echartsData.ydata.push(item.serviceName)
          }
        }).catch(() => {});
        this.macDialogMac = mac;
        let params={mac:this.macDialogMac,page:this.pageSwjl.currentPage,pageSize:this.pageSwjl.size}
        this.netWorkLog(params);
      },
      netWorkLog(params){
//        this.pageSwjl.currentPage = 1;
        seoHttp.netWorkLog(params).then((result)=>{
          this.macTable = result.data;
          this.pageSwjl.total=result.totalRecord;
          this.pageSwj2.total = Math.ceil(result.totalRecord/this.pageSwjl.size);
        }).catch(() => {});
      },
      handleSizeChangeSwjl(val) {
        this.pageSwjl.size = val;
        this.netWorkLog({mac:this.macDialogMac,page:this.pageSwjl.currentPage,pageSize:this.pageSwjl.size});
      },
      handleCurrentChangeSwjl(val) {
        this.pageSwjl.currentPage = val;
        this.netWorkLog({mac:this.macDialogMac,page:this.pageSwjl.currentPage,pageSize:this.pageSwjl.size});
      },
      handleCurrentChangeSwjl2(val) {
        this.pageSwj2.currentPage = val;
        this.netWorkLog({mac:this.macDialogMac,page:this.pageSwj2.currentPage,pageSize:this.pageSwj1.size});
      },
      handleCurrentChangeSwjl3(val){
        this.pageSwj3.currentPage = val;
        this. getMacList(this.pageSwj3.currentPage ,this.pageSwj3.size)
      },
      showMac(){
          this.transitionShow=!this.transitionShow;
      },
      showTabList(mac){
        //   显示mac tabs
        this.macDialogCk(mac)
        this.dialogMacDetial=true;
      },
      cellStyle({ row, rowIndex}) {
        return 'textAlign:center,height:30px'
      },
      changeKey(){
        //    列为重点
        let arr=[];
        for(let item of this.gridData){
          if(item.keyPeople==true){
            arr.push(item.mac)
          }
        }
        Arrangement.keyPeople({
          macs:arr.join(','),
          type:1
        }).then(res=>{
          if(res.code==200){
            this.$message({
              message: '列为重点成功',
              type: 'success'
            });
          }
        }).catch(res=>{})
      },
      getMacList(pageNum,pageSize){
        var flag=0;
        if(typeof this.macListCoods=='string'){
            //  第一次加载
          flag=1;
        }
        let servicecode=JSON.parse(this.defaultMac.executecontrol_way_id)[0].serviceCodes[0];
        this.gridData=[];
        this.macListCoods=[];
        Arrangement.macList({
          id:this.defaultMac.id,
          servicecode:servicecode,
          pageNum:pageNum,
          pageSize:pageSize,
        }).then(res=>{
          //console.log("grid",res.data)
          this.gridData=res.data;
          this.pageSwj3.total=res.totalRecord;
          this.macListCoods=this.gridData;
          for(let i of this.gridData){
              i.minTime=this.getDateByDay((i.minTime)*1000)
          }
          if(flag==1){
            setTimeout(function(){
              var ifr2=document.querySelector("#map_view_main").contentWindow;
              ifr2.resetData();
            },3000)
          }else{
            var ifr2=document.querySelector("#map_view_main").contentWindow;
            ifr2.resetData();
          }
        }).catch(res=>{
        })
      },


      getData(params,flag){
        Arrangement.search(params).then((result)=>{
          this.taskList = result.data;
          if(flag){
            this.defaultMac=this.taskList[0];
          }
        }).catch(() => {});
      },
      searchTask(){
        let params={
          taskName:this.taskName,
          pageNum:this.currentPage,
          pageSize:this.pageSize,
        };
        if(this.detialData!=0){
          params.status=this.detialData;
        }
        this.getData(params)
      },
      showTaskDetial(item){
        this.dialogTaskDetial=true;
        this.currentTaskId=item.id;
        this.getTaskDeial(item.id)
      },
      showMacList(item){
        this.defaultMac=item;
        this.pageSwj3.currentPage = 1
      },
      closeTaskDetail(){
        this.dialogTaskDetial=false;
        this.getData({
          pageNum:this.currentPage,
          pageSize:this.pageSize,
        })
      },
      getCoods(name,code,status){
        let serviceName=this.editableTabs[this.currentTab].content.serviceName;
        let serviceCodes=this.editableTabs[this.currentTab].content.serviceCodes;
        let baseNames,baseCode;
        let res=new RegExp(name,"g");
        let resCode=new RegExp(code,"g");
        if(status==0){
          //  取消
          let res=new RegExp(name+',',"g");
          let resCode=new RegExp(code+',',"g");
          baseNames=serviceName.replace(res,'');
          baseCode=serviceCodes.replace(resCode,'');
        }else{
          //  选中
          if(!res.exec(serviceName)){
            //   去重
            baseNames=serviceName+name+',';
            baseCode=serviceCodes+code+',';
          }
        }
                this.editableTabs[this.currentTab].content.serviceName=baseNames;
               this.editableTabs[this.currentTab].content.serviceCodes=baseCode;

      },
      getDateByDay(time) {
        //  转化时间格式
        let oDate = new Date(time);
        return '' + oDate.getFullYear() + '-' + toTen(oDate.getMonth() + 1) + '-' + toTen(oDate.getDate());

        function toTen(number) {
          return parseInt(number) < 10 ? '0' + number : '' + number;
        }
      },
      handleTabsEdit(targetName, action) {
        if (action === 'add') {
          let len=this.editableTabs.length;
          if(len>=3){
            this.$message({
              message: '区域最多添加3个',
              type: 'warning'
            });
            return ;
          }
          let ifr3=document.querySelector("#map_add_view").contentWindow;
            ifr3.location.reload();
          let newTabName = ++this.tabIndex + '';
          this.editableTabs.push({
            title:  '区域' + (this.editableTabs.length + 1),
            name: newTabName,
            content: {
              startTime: "00:00",
              endTime: "24:00",
              sel:1,
              count:'',
              serviceCodes:'',
              serviceName:''
            }
          });
          this.editableTabsValue = newTabName;
          this.currentTab=newTabName;
          let str=this.editableTabs[this.currentTab].content.serviceCodes;
          let str1=str.substr(0,-1+len);
          if(str1!==''){
            str1=str1.split(",");
            this.lastCodes=str1;
          }
        }
        if (action === 'remove') {
          let tabs = this.editableTabs;
          let activeName = this.editableTabsValue;
          if (activeName === targetName) {
            tabs.forEach((tab, index) => {
              if (tab.name === targetName) {
                let nextTab = tabs[index + 1] || tabs[index - 1];
                if (nextTab) {
                  activeName = nextTab.name;
                }
              }
            });
          }
          this.editableTabsValue = activeName;
          this.editableTabs = tabs.filter(tab => tab.name !== targetName);
        }
      },
      addTask() {
        this.baseData.addTask.push({inputTarget: ''})
      },
      delTask(index) {
        this.baseData.addTask.splice(index, index + 1);
      },
      areaClick(tab, event) {
        this.currentTab=tab.index;
        let ifr3=document.querySelector("#map_add_view").contentWindow;
         ifr3.location.reload();
        let str=this.editableTabs[this.currentTab].content.serviceCodes;
        let len=this.editableTabs[this.currentTab].content.serviceCodes.length;
        let str1=str.substr(0,-1+len);
        if(str1!==''){
          str1=str1.split(",");
          this.lastCodes=str1;
        }
      },
      searchCode() {
        let ifr3=document.querySelector("#map_add_view").contentWindow;
        ifr3.searchPlace(this.placeCode)
//        let params={};
//        let res=/^[0-9]+$/g;
//          if(res.exec(this.placeCode)){
//            params.serviceCode=this.placeCode;
//          }else{
//            params.serviceName=this.placeCode;
//          }
//        Arrangement.searchPlace(params).then(res=>{
//
//        }).catch()
      },
      startControl() {
        //   基本信息的参数
        let params = this.baseData;
        if(!params.taskName){
          this.$message({
            message: '请输入任务名称',
            type: 'warning'
          });
          return ;
        }
        if(!(params.taskEndTimeStr && params.taskBeginTimeStr)){
          this.$message({
            message: '请输入布控时间',
            type: 'warning'
          });
          return ;
        }
        let areas = [];
        let str = '';
        if (params.addTask.length==1 && params.addTask[0].inputTarget=='') {
            str='';
        }else{
          for (let i of params.addTask) {
            str += i.inputTarget + ',';
          }
        }
        if (params.targetType == 0) {
          this.$message({
            message: '请选择布控类型',
            type: 'warning'
          });
          return ;
        }
        params.remards = str;
        //   区域信息数据
        for (let item of this.editableTabs) {
          if(!item.content.count){
            this.$message({
              message: '请输入出现次数',
              type: 'warning'
            });
            return ;
          }
          if(item.content.serviceCodes!=''){
            item.content.serviceCodes=item.content.serviceCodes.split(",");
            item.content.serviceCodes.forEach(function(val,ind){
              if(val==''){
                item.content.serviceCodes.splice(ind,1)
              }
            })
            areas.push(item.content)
          }else{
            item.content.serviceCodes=[];
            areas.push(item.content)
          }
        }

        params.executecontrolWayId = JSON.stringify(areas);
        Arrangement.addTask(params).then(res=>{
          this.dialogAddTask=false;
          this.getData({
            pageNum:this.currentPage,
            pageSize:this.pageSize,
          })
        }).catch(res=>{})
      },

      getTaskDeial(id){
        //   得到任务详情数据
        Arrangement.getTaskDetial({id:id}).then((result)=>{
          this.detialData=result.data[0];
          this.coods=this.detialData.result;
          if(this.detialData.task_begin_time){
            this.detialData.task_begin_time=this.getDateByDay(this.detialData.task_begin_time);
          }
          if(this.detialData.task_end_time){
            this.detialData.task_end_time=this.getDateByDay(this.detialData.task_end_time);
          }
          let obj=JSON.parse(this.detialData.executecontrol_way_id);
          obj.forEach(function(val,index){
            val.name=index.toString();
            val.label="区域"+(index+1);
          })
          this.resArr=obj;
          this.getAreaData(0);
        }).catch(() => {});
      },
      getAreaData(ind){
        let serviceCodes=this.resArr[ind].serviceCodes;     // 每个区域的场所code的集合
        let prototypes=Object.keys(this.coods);   //  保存所有code对应的经纬度详细信息
        this.currentAreaMes=[];
        let that=this;
        prototypes.forEach(function(val1){
            serviceCodes.forEach(function(val2){
              if(val1==val2){
                that.currentAreaMes.push(that.coods[val1][0])
              }
            })
        })
        if(ind==0){
          setTimeout(function(){
            const ifr1=document.querySelector("#map_view_detial").contentWindow;
            ifr1.resetData();},2000)

        }else{
          const ifr1=document.querySelector("#map_view_detial").contentWindow;
          ifr1.resetData();
        }

      },
      detialTab(tab, event) {
//        const ifr1=document.querySelector("#map_view_detial").contentWindow;
//        ifr1.resetData();
        this.getAreaData(tab.index);
      },
      detialDel(){
          // 布控
          Arrangement.detelTask({id:this.currentTaskId}).then((result)=>{
            if(result.code==200){
              this.$message({
                message: '删除成功',
                type: 'success'
              });
            }
            this.dialogTaskDetial=false;
            this.getData({
              pageNum:this.currentPage,
              pageSize:this.pageSize,
            },1)
          }).catch(() => {});
      }
    },
    watch:{
      'editableTabs':{
        handler:function(val,oldVal){
          let itemData=val[this.currentTab].content;
          switch(itemData.sel)
          {
            case 1:
              itemData.startTime="00:00:00";
                itemData.endTime="24:00:00";
              break;
            case 2:
              itemData.startTime="20:00:00";
              itemData.endTime="06:00:00";
              break;
            case 3:
              itemData.startTime="05:00:00";
              itemData.endTime="19:00:00";
              break;
            case 4:
              itemData.startTime="";
              itemData.endTime="";
              break;
          }
        },
        // 深度观察
        deep:true
      },
      'defaultMac':{
        handler:function(val){
            this.getMacList(this.pageSwj3.currentPage,this.pageSwj3.size);
        },
        // 深度观察
        deep:true
      },
//      'currentTab':function(){
//      }
    },
    mounted(){
      window.vmObj = () => {
        return this
      };
      this.getData({
        pageNum:this.currentPage,
        pageSize:this.pageSize,
      },1)
    },
    components:{
      EchartsMac,
      pageCtrl
    }
  }
</script>
