<template>
  <div class="role-wrap">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>设置</el-breadcrumb-item>
      <el-breadcrumb-item>角色管理</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="manage-cont">

      <setupNav></setupNav>

      <div class="manage-nr">
        <el-card class="box-card">
          <el-button type="primary" size="small" @click="addRole">添加业务角色</el-button>
          <el-input placeholder="请输入角色名称" v-model="roleName" size="small" class="input-with-seach ml20">
            <el-button slot="append" icon="el-icon-search" @click="searchList"></el-button>
          </el-input>
          <el-table
            class="mt20"
            :data="tableData"
            tooltip-effect="dark"
            style="width: 100%"
            stripe>
            <el-table-column
              label="序号"
              type="index"
              width="50">
            </el-table-column>
            <el-table-column
              label="角色名称"
              show-overflow-tooltip>
              <template slot-scope="scope">
                <el-button type="text" @click="getRole(scope.row.id)">{{scope.row.roleName}}</el-button>
              </template>
            </el-table-column>
            <el-table-column
                label="备注"
                prop="roleDesc"
                show-overflow-tooltip>
              </el-table-column>
            <el-table-column
              fixed="right"
              label="操作"
              width="100">
              <template slot-scope="scope">
                <el-button type="text" size="small" v-if="scope.row.deleteStatus == '1'" @click="delRole(scope.row.id)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="page.currentPage"
            :page-sizes="page.sizes"
            :page-size="page.size"
            layout="total, sizes, prev, pager, next, jumper"
            :total="page.total">
          </el-pagination>
        </el-card>
      </div>

    </div>

    <el-dialog
      title="添加业务角色"
      :visible.sync="addRoledialog"
      width="60%">
      <el-form ref="addRoleFrom" :model="addRoleFrom" label-width="120px">
        <el-form-item label="角色名称：">
          <el-input v-model="addRoleFrom.roleName"></el-input>
        </el-form-item>
        <el-form-item label="角色描述：">
          <el-input type="textarea" v-model="addRoleFrom.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <div class="popcont">
        <el-table
          :data="popTableData"
          tooltip-effect="dark"
          style="width: 100%"
          border>
          <el-table-column
            label="菜单名称"
            prop="menuName"
            width="100">
          </el-table-column>
          <el-table-column label="操作功能">
            <template slot-scope="scope">
              <el-checkbox-group v-model="addRoleFrom.permissions">
                <el-checkbox v-for="popCheck in scope.row.permissions" :label="popCheck.permissionId" :key="popCheck.permissionId">{{popCheck.permissionName}}</el-checkbox>
              </el-checkbox-group>
            </template>
          </el-table-column>

        </el-table>

      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addRoledialog = false">取 消</el-button>
        <el-button type="primary" @click="addRoleSave">保 存</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="编辑业务角色"
      :visible.sync="editRoledialog"
      width="60%">
      <el-form ref="addRoleFrom" :model="editRoleFrom" label-width="120px">
        <el-form-item label="角色名称：">
          <el-input v-model="editRoleFrom.roleName"></el-input>
        </el-form-item>
        <el-form-item label="角色描述：">
          <el-input type="textarea" v-model="editRoleFrom.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <div class="popcont">
        <el-table
          :data="popTableData"
          tooltip-effect="dark"
          style="width: 100%"
          border>
          <el-table-column
            label="菜单名称"
            prop="menuName"
            width="100">
          </el-table-column>
          <el-table-column label="操作功能">
            <template slot-scope="scope">
              <el-checkbox-group v-model="editRoleFrom.permissions">
                <el-checkbox v-for="popCheck in scope.row.permissions" :label="popCheck.permissionId" :key="popCheck.permissionId">{{popCheck.permissionName}}</el-checkbox>
              </el-checkbox-group>
            </template>
          </el-table-column>

        </el-table>

      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editRoledialog = false">取 消</el-button>
        <el-button type="primary" @click="editRoleSave">保 存</el-button>
      </span>
    </el-dialog>

  </div>


</template>

<script type="text/javascript">
  import setupNav from 'components/setup/nav';
  import {roleHttp} from '../../api/setupHttp.js';
  export default {
    name: 'home',
    data(){
      return {
        editRoleFrom:{
          roleName:'',
          roleDesc:'',
          permissions:[]
        },
        editRoledialog:false,
        addRoleFrom:{
          roleName:'',
          roleDesc:'',
          permissions:[]
        },
        addRoledialog:false,
        roleName:'',
        tableData: [],
        popTableData: [],

        page:{
          currentPage:1,
          sizes:[10, 20],
          size:10,
          total:0
        }
      }
    },
    created:function(){
      this.initData();
    },
    methods:{
      initData(){
        this.getList();
      },
      getList(){
        roleHttp.getList({page:this.page.currentPage,pageSize:this.page.size,roleName:this.roleName}).then((result)=>{
          this.tableData = result.data;
          this.page.total = result.totalRecord;
        }).catch(() => {});
      },
      searchList(){
        this.getList();
      },
      getRoleCheck(){
        roleHttp.getRoleCheck().then((result)=>{
          this.popTableData = result.data;
        }).catch(() => {});
      },
      handleSizeChange(val) {
        this.page.size = val;
        this.getList();
        // console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        this.page.currentPage = val;
        this.getList();
        // console.log(`当前页: ${val}`);
      },
      addRole(){
        this.getRoleCheck();
        this.addRoledialog = true;
      },
      addRoleSave(){
        roleHttp.addRole(this.addRoleFrom).then((result)=>{
          this.getList();
          this.addRoledialog = false;
        }).catch(() => {});
      },
      delRole(id){
        this.$confirm('是否删除该用户?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          roleHttp.delRole({id:id}).then((result)=>{
            this.getList();
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
          }).catch(() => {});
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      getRole(id){
        roleHttp.getRole({roleId:id}).then((result)=>{
          this.getRoleCheck();
          this.editRoleFrom = result.data;
          this.editRoledialog = true;
        }).catch(() => {});
      },
      editRoleSave(){
        roleHttp.editRole(this.editRoleFrom).then((result)=>{
          this.editRoledialog = false;
          this.getList();
          this.$message({
            type: 'success',
            message: '修改成功!'
          });
        }).catch(() => {});
      }
    },
    components:{
      setupNav
    }
   }
</script>

<style lang="scss" scoped>
  @import "~assets/css/lib-variable";
  @import "~assets/css/lib-mixin";

  .role-wrap{
    width:  $site-width;
    min-width: $site-minwidth;
    min-height:$site-minheight;
    padding:0 20px;
    box-sizing:border-box;
  }
  .popcont{
    overflow:hidden;margin-top:20px;
    .popcont-l{float:left;width:30%;}
    .popcont-r{margin-left:32%;}
  }
</style>



