<template>
  <div class="role-wrap">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>设置</el-breadcrumb-item>
      <el-breadcrumb-item>操作日志</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="manage-cont">

      <setupNav></setupNav>

      <div class="manage-nr">
        <el-card class="box-card">
          <!-- <el-input placeholder="请输入用户名称" v-model="userName" size="small" class="input-with-seach">
            <el-button slot="append" icon="el-icon-search" @click="searchList"></el-button>
          </el-input> -->
          <el-table
            class="mt20"
            :data="tableData"
            tooltip-effect="dark"
            style="width: 100%"
            stripe>
            <el-table-column
              label="序号"
              type="index"
              width="50">
            </el-table-column>
            <el-table-column
              label="用户名"
              prop="username"
              show-overflow-tooltip>
            </el-table-column>
            <el-table-column
              label="部门业务编码"
              prop="deptIdCard"
              show-overflow-tooltip>
            </el-table-column>
            <el-table-column
              label="用户所属机构"
              prop="deptNm"
              show-overflow-tooltip>
            </el-table-column>
            <el-table-column
              label="时长"
              prop="duration"
              show-overflow-tooltip>
            </el-table-column>
            <el-table-column
              label="职务"
              prop="duty"
              show-overflow-tooltip>
            </el-table-column>
            <el-table-column
              label="IP地址"
              prop="ipAddress"
              show-overflow-tooltip>
            </el-table-column>
            <el-table-column
              label="访问模块"
              prop="module"
              show-overflow-tooltip>
            </el-table-column>
            <el-table-column
              label="模块操作"
              prop="operation"
              show-overflow-tooltip>
            </el-table-column>
            <el-table-column
              label="真实姓名"
              prop="realName"
              show-overflow-tooltip>
            </el-table-column>
            <el-table-column
              label="请求参数"
              prop="requestParams"
              show-overflow-tooltip>
            </el-table-column>
            <el-table-column
              label="请求时间"
              prop="requestTime"
              show-overflow-tooltip>
            </el-table-column>
          </el-table>
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="page.currentPage"
            :page-sizes="page.sizes"
            :page-size="page.size"
            layout="total, sizes, prev, pager, next, jumper"
            :total="page.total">
          </el-pagination>
        </el-card>
      </div>

    </div>

  </div>


</template>

<script type="text/javascript">
  import setupNav from 'components/setup/nav';
  import {ologHttp} from '../../api/setupHttp.js';
  export default {
    name: 'home',
    data(){
      return {
        userName:'',
        tableData: [],

        page:{
          currentPage:1,
          sizes:[10, 20],
          size:10,
          total:10
        }
      }
    },
    created:function(){
      this.initData();
    },
    methods:{
      initData(){
        this.getList();
      },
      getList(){
        ologHttp.getList({page:this.page.currentPage,pageSize:this.page.size}).then((result)=>{
          console.log(result)
          this.tableData = result.data;
          this.page.total = result.totalRecord;
        }).catch(() => {});
      },
      handleSizeChange(val) {
        this.page.size = val;
        this.getList();
        // console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        this.page.currentPage = val;
        this.getList();
        // console.log(`当前页: ${val}`);
      },
      searchList(){

      }
    },
    components:{
      setupNav
    }
   }
</script>

<style lang="scss" scoped>
  @import "~assets/css/lib-variable";
  @import "~assets/css/lib-mixin";

  .role-wrap{
    width:  $site-width;
    min-width: $site-minwidth;
    min-height:$site-minheight;
    padding:0 20px;
    box-sizing:border-box;
  }
  .popcont{
    overflow:hidden;margin-top:20px;
    .popcont-l{float:left;width:30%;}
    .popcont-r{margin-left:32%;}
  }
</style>



