import xhr from '../../static/api'
import xhr2 from '../../static/api2'

export const homeHttp = {
  bkList(params) {
    return xhr2.post("/executecontrol/contorlMessageNotice", params)
  },
  collectMacNum(params) {
    return xhr.get("/homepage/collectMacNum", params)
  },
  keyPeopleCount(params) {
    return xhr.get("/homepage/keyPeopleCount", params)
  },
  equipOnline(params) {
    return xhr.get("/homepage/equipOnline", params)
  },
  placeOnline(params) {
    return xhr.get("/homepage/placeOnline", params)
  },
  areaCount(params) {
    return xhr2.get("/homepage/areaCount", params)
  },
  addNewly(params) {
    return xhr.get("/homepage/addNewly", params)
  },
  selExecutecontrols(params) {
    return xhr2.post("/homepage/executecontroTasklList", params)
  },
  equipOffline(params) {
    return xhr2.get("/homepage/equipOffline", params)
  }
}



