<template>
  <div class="wrap" style="height:50%;width:10%;">
        <div class="arrows"  ref='pointer'  style="height:195px;width:100%">
          <div>
            <i class="arrows-item" ref='icon'></i>
            <i :class="active ? 'arrows-item-list ' : 'arrows-item-list1 ' " ></i>
          </div>
          <div class="content" >
            <b class="content-item" ref='content'>{{message}}</b>
            <!-- <i class="dota" >%</i> -->
          </div>
        </div>
      <div class="con">0</div>
  </div>
</template>
  <script>
import * as d3 from 'd3'
export default {
  name: 'pointer',
  data() {
    return {}
  },
  props: {
    active: true,
    message: 0,
    array: ''
  },
  methods: {
    _cerateArc(r, R, start, end) {
      let me = this
      let arc = d3
        .arc()
        .innerRadius(r)
        .outerRadius(R)
        .startAngle(start)
        .endAngle(end)
      return arc()
    },
    loading() {
      let me = this
      me.width = 240
      me.height = 205
      me.R = 85
      me.r = 0
      me.M = Math.PI
      me.smallR = me.R + 5
      me.startAngle = me.M / 5
      me.scaleAngleFor = -me.M / 2
      me.scaleAngleTo = me.M / 2
      /**svg */
      let svg = d3
        .select(me.$refs.pointer)
        .append('svg')
        .attr('width', '100%')
        .attr('height', '100%')
        .attr('preserveAspectRatio', 'xMidYMid meet')
        .attr('viewBox', '0 0 240 255')

      if (this.array === true) {
        /**渐变色 */
        this.linearGradient = svg
          .append('linearGradient')
          .attr('id', 'linearColor')
          .attr('x1', '0%')
          .attr('y1', '0%')
          .attr('x2', '0%')
          .attr('y2', '100%')
        let stop1 = this.linearGradient
          .append('stop')
          .attr('offset', '0%')
          .style('stop-color', '#4db9f5')
        let stop2 = this.linearGradient
          .append('stop')
          .attr('offset', '100%')
          .style('stop-color', '#257cd7')
      } else {
        /**渐变色 */
        this.linearGradient = svg
          .append('linearGradient')
          .attr('id', 'linearColor2')
          .attr('x1', '0%')
          .attr('y1', '0%')
          .attr('x2', '0%')
          .attr('y2', '100%')
        let stop1 = this.linearGradient
          .append('stop')
          .attr('offset', '0%')
          .style('stop-color', '#fedd86')
        let stop2 = this.linearGradient
          .append('stop')
          .attr('offset', '100%')
          .style('stop-color', '#f6c345')
      }

      let data = [0, 20, 40, 60, 80, 100].map((s, i) => {
        let start = me.scaleAngleFor + i * me.startAngle
        let end = start + me.startAngle - 2 * Math.PI / 180
        let val = s
        return {
          start,
          end,
          val
        }
      })

      /*画半圆弧------ */
      let g = svg.append('g').attr('transform', `translate(${this.width / 2},${this.height / 2})`)
      g
        .append('text')
        .attr('dx', -103)
        .attr('dy', 3)
        .attr('fill', '#575c63')
        .attr('font-size', 12)
        .text(0 + '%')
      g
        .append('path')
        .attr('d', function(d, i) {
          return me._cerateArc(me.r, me.R, -me.M / 2, me.M / 2)
        })
        .attr('fill', '#eee')

      /**画文字的弧 */
      let gText = svg
        .selectAll('g')
        .data(data)
        .enter()
      let gg = gText.append('g').attr('transform', `translate(${me.width / 2},${me.height / 2})`)
      /**根据数据的------弧  */
      gg
        .append('path')
        .attr('d', function(d, i) {
          if (i > 5) {
            return
          }
          let path = me._cerateArc(me.smallR - 2, me.smallR, d.start, d.end - Math.PI / 90)
          return path
        })
        .attr('fill', '#fff')
      /**添加文字---------- */
      gg
        .append('text')
        .attr('dy', function(d, i) {
          if (i == 0) {
            return 3
          }
          if (i == 1) {
            return 5
          }
          if (i == 2) {
            return 10
          }
          if (i == 3) {
            return 9
          }
          if (i == 4) {
            return 4
          }
          if (i == 5) {
            return 0
          }
        })
        .attr('dx', function(d, i) {
          if (i == 0) {
            return -9
          }
          if (i == 1) {
            return -12
          }
          if (i == 2) {
            return -9
          }
          if (i == 3) {
            return -13
          }
          if (i == 4) {
            return -10
          }
          if (i == 5) {
            return -20
          }
        })
        .attr('fill', '#575c63')
        .attr('font-size', 12)
        .attr('x', function(d, i) {
          return (me.smallR + 10) * Math.sin(d.start)
        })
        .attr('y', function(d, i) {
          return -(me.smallR + 10) * Math.cos(d.start)
        })
        .text(function(d, i) {
          return d.val + '%'
        })
      let transG = svg
        .append('g')
        .attr('class', 'changeTransgorm')
        .attr('transform', `translate(${me.width / 2},${me.height / 2})`)
        .append('path')
        .transition()
        .duration(1000)
        .delay(1000)
        .ease(d3.easeLinear)
        .attrTween('d', function(d, i, a) {
          return function(t) {
            return me._cerateArc(me.r, me.R, -me.M / 2, me.M / 100 * me.message * t - me.M / 2)
          }
        })
        .attr('fill', function() {
          return 'url(#' + me.linearGradient.attr('id') + ')'
        })
      // /***旋转角度----------- */
      let angleData = 1.8 * this.message - 160 + 20
      setTimeout(function() {
        me.$refs.icon.style.transform = `rotate(${angleData}deg)`;
         me.$refs.icon.style.display = 'none'
      }, 1000)

      /**中心点----- */
      me.$refs.icon.style.transformOrigin = '18% 18%'
    }
  },
  mounted() {
    this.loading()
  }
}
</script>
  <style lang="scss" scoped>
.wrap {
}

.arrows-item {
  width: 100%;
  height: 40%;
  background: url('./img/arrow.png') no-repeat;
  background-size: 40% 40%;
  display: block;
  position: absolute;
  left: 32%;
  top: 11%;
  z-index: 2;
  transform: rotate(-160deg);
  transition: all 1s linear;
}
.arrows-item-list {
  width: 100%;
  height: 25%;
  background: url('./img/circle1.png') no-repeat;
  background-size: 40% 40%;
  display: block;
  position: absolute;
  left: 39%;
  top: 14%;
  z-index: 9;
}
.arrows-item-list1 {
  width: 100%;
  height: 25%;
  background: url('./img/circle2.png') no-repeat;
  background-size: 40% 40%;
  display: block;
  position: absolute;
  left: 39%;
  top: 14%;
  z-index: 9;
}
.content {
  position: absolute;
  left: 39%;
  top: 16%;
  z-index: 10;
}
.content-item {
  font-size: 12px;
  margin-left: 10%;
  margin-top: 2px;
  color: #2d3640;
}
// .dota {
//   font-size: 12px;
//   margin-left: -5px;
//   margin-top: 2px;
//   color: #2d3640;
// }
.con {
  position: absolute;
  left: 53px;
  top: 141px;
  font-size: 16px;
}
.content-foot {
  position: absolute;
  top: 30%;
}
</style>


