<template>

  <div class="home-wrap">

    <el-row :gutter="10">
      <el-col :span="7">
        <div class="module">
          <ul v-if="bkList.length !== 0" class="bk-module">
            <li v-for="item in bkList">
              <p><router-link to="clothControl">{{item.taskName}}</router-link></p>
              <p>MAC {{item.mac}}</p>
              <p>{{item.startTime |formatDate}} {{item.serviceName}}</p>
            </li>
          </ul>
          <div v-if="bkList.length == 0" class="bk-nodata">暂无已完成布控任务</div>
          <div class="bk-icon"><router-link class="bk-an" to="clothControl">查看全部</router-link></div>
        </div>
        <div class="module style='width:100%;' zxl-module">
          <div>
           <Pointer class="pointerRight" :active='true' :message='equipOnlinReat' :array="true" ></Pointer>
          <div class='equient'>今日设备在线率</div>
          </div>
          <div>
          <Pointer class="pointerLeft" :active='false' :message='placeOnlineReat' :array="false" ></Pointer>
           <div class="place">今日场所在线率</div>
        </div>
        </div>
      </el-col>
      <el-col :span="10">
        <div class="map-module">
          <ul class="list-item">
            <li><img src='./img/person.png'><span>布控人员</span></li>
            <li><img src='./img/equipment .png'><span>离线设备</span></li>
          </ul>
          <ul class="cards">
              <li>
                  <h4>当前发现布控人员</h4>
                  <div><i class="text">120人</i></div>
              </li>
              <li>
                  <h4>当前发现离线设备</h4>
                  <div><i class="text">{{officeOline}}台</i></div>
              </li>
          </ul>
           <div v-for='(items,index) in mapList' class="mapIconBg" ref="mapIconBg" @mouseover="show($event,index)" @mouseout="hide($event,index)" >
             <a :class=" 'mapIconBg' + (index + 1) " ref="icons" ></a>
            <div class="tooltipBox " ref= 'tooltipBox' @timer="idtimer(index)" >
              <h3 >{{items.areaName}}</h3>
              <p>{{items.placeCount}}个场所  、 {{items.equipCount}}个设备</p>
              <p>共采集{{items.macCount}}个M*</p>
              <p>发现{{items.peopleCount}}条布控人活动轨迹</p>
          </div>
           </div>
        </div>
      </el-col>
      <el-col :span="7">
        <div class="module mac-module">
          <div class="macm-top">
            <div class="macm-top-l">
              <p>本周采集Mac</p>
              <p>{{todayMac}}</p>
            </div>
            <div class="macm-top-r">
              <p>总Mac</p>
              <p>{{allMac}}</p>
            </div>
          </div>
          <div class="macm-chart">
            <!-- <span style="color:#333;">mac采集图</span> -->
            <SingleBar></SingleBar>
          </div>
        </div>
        <div class="module zdr-module">
          <span style="color:#333;">
            <LineChart></LineChart>
          </span>
        </div>
        <div class="module newmac-module" v-if="addNewlyData">
          <ul v-for="items in addNewlyData">
            <li>今日新增{{items.name}} {{items.todayAdd}}，总{{items.name}} {{items.totalCount}}</li>
            <!-- <li>今日新增Mac{{newAll.newMac}}，总Mac{{newAll.allMac}}</li>
            <li>今日新增手机号{{newAll.newTel}}，总手机号{{newAll.allTel}}</li>
            <li>今日新增IMEI{{newAll.newImei}}，总IMEI{{newAll.allImei}}</li>
            <li>今日新增IMSI{{newAll.newImsi}}，总IMSI{{newAll.allImsi}}</li> -->
          </ul>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script type="text/javascript">
import { homeHttp } from '../../api/homeHttp.js'
import SingleBar from '../../components/echarts/singleBar'
import Pointer from 'components/echarts/pointer'
import LineChart from '../../components/echarts/lineChart'
export default {
  name: 'home',
  filters: {
    formatDate(time) {
      var date = new Date(time*1000)
      let Y = date.getFullYear() + '-'
      let M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-'
      let D = date.getDate() + ' '
      let h = date.getHours() + ':'
      let m = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes() + ':'
      let s = date.getSeconds()
      return Y + M + D + h + m + s
    }
  },
  data() {
    return {
      bkList: [],
      equipOnlinReat: '',
      placeOnlineReat: '',
      addNewlyData: '',
      // x: ['758', '830', '1080', '908', '1109'],
      x: ['40%', '45%', '59%', '48%', '58%'],
      y: ['311', '154', '288', '288', '500'],
      officeOline: '',
      newAll: {
        newMac: '3221',
        allMac: '3243221',
        newTel: '212',
        allTel: '123231',
        newImei: '2132',
        allImei: '32133',
        newImsi: '13232',
        allImsi: '1341312'
      },
      todayMac: '122113',
      allMac: '129807123',
      mapList: []
    }
  },
  created: function() {
    this.initData()
    // window.vmObj = () => {
    //   return this
    // }
  },
  methods: {
    /**鼠标滑过icon */
    show($event, index) {
      $event.cancelBubble = true
      let tooltipBox = this.$refs.tooltipBox[index]
      tooltipBox.style.display = 'block'
      tooltipBox.style.left = this.x[index]
      tooltipBox.style.top = this.y[index] + 'px'
    },
    hide($event, index) {
      $event.cancelBubble = true
      let tooltipBox = this.$refs.tooltipBox[index]
      tooltipBox.style.display = 'none'
    },

    initData() {
      this.selExecutecontrols()
      this.equipOnline()
      this.placeOnline()
      this.areaCount()
      this.addNewly()
      this.equipOffline()
    },
    selExecutecontrols() {
      homeHttp
        .selExecutecontrols()
        .then(result => {
          //console.log(result.data[0].data)
          this.bkList = result.data[0].data
        })
        .catch(() => {})
    },
    addNewly() {
      homeHttp
        .addNewly()
        .then(result => {
          this.addNewlyData = result.data
        })
        .catch(() => {})
    },
    /**设备在线率 */
    equipOnline() {
      homeHttp
        .equipOnline()
        .then(result => {
          if (result.data.totalCount === 0) {
            result.data.totalCount = 1
          }
          this.equipOnlinReat = Number(result.data.onlineCount / result.data.totalCount).toFixed(1)*100
        })
        .catch(() => {})
    },
    /**场所在线 */
    placeOnline() {
      homeHttp
        .placeOnline()
        .then(result => {
          if (result.data.totalCount === 0) {
            result.data.totalCount = 1
          }
          this.placeOnlineReat = Number(result.data.onlineCount / result.data.totalCount).toFixed(1)*100
        })
        .catch(() => {})
    },
    /**地图 */
    areaCount() {
      homeHttp
        .areaCount()
        .then(result => {
          this.mapList = result.data
        })
        .catch(() => {})
    },
    /**离线设置 */
    equipOffline() {
      homeHttp
        .equipOffline()
        .then(result => {
          this.officeOline = result.data.offlineCount
        })
        .catch(() => {})
    }
  },
  components: {
    SingleBar,
    LineChart,
    Pointer
  },
  mounted() {}
}
</script>

<style lang="scss" scoped>
@import '~assets/css/lib-variable';
@import '~assets/css/lib-mixin';
.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  li {
    text-align: center;
    margin-bottom: 15px;
    margin-right: 34px;
    width: 200px;
    height: 100px;
    border: 1px solid;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
    h4 {
      line-height: 40px;
      font-size: 18px;
      color: #fff;
    }
    div {
      font-size: 18px;
      height: 60px;
      display: flex;
      justify-content: center;
      color: #000;
      align-items: center;
      .text {
        height: 27px;
      }
    }
  }
  li:nth-child(1) {
    border-color: #157acf;
    h4 {
      background-color: #157acf;
      color: #fff;
    }
    div {
      color: #157acf;
    }
  }
  li:nth-child(2) {
    border-color: #0cabe2;
    h4 {
      background-color: #0cabe2;
      color: #fff;
    }
    div {
      color: #0cabe2;
    }
  }
}
