  margin-top: 10px;
}
.home-wrap {
  width: $site-width;
  min-width: $site-minwidth;
  min-height: $site-minheight;
  padding: 40px;
  box-sizing: border-box;
  /*background-image: url('./../../../static/img/homeBg.jpg');*/
  background-size: cover;
  background-repeat: no-repeat;
}
.module {
  border: 1px solid #d2d2d2;
  background: #fff;
}
.bk-module {
  height: 505px;
  overflow-y: scroll;
  li {
    color: #999;
    padding: 5px 15px;
    border-left: 4px solid #fff;
    font-size: 12px;
    border-bottom: 1px solid #d2d2d2;
    p {
      line-height: 24px;
      height: 24px;
