      }
    }
  }
  .macm-top-r {
    float: right;
    background: #acede8;
    width: 49%;
    height: 80px;
    text-align: center;
    color: #045466;
    font-size: 30px;
    line-height: 50px;
    border-radius: 5px 5px 0 0;
    overflow: hidden;
    p {
      &:first-child {
        background: #2fd1c6;
        color: #2d3640;
