}
.zdr-module {
  height: 300px;
  margin-top: 10px;
}
.newmac-module {
  margin-top: 10px;
  // padding: 20px 0;
  overflow-y: scroll;
  height: 200px;
  li {
    height: 40px;
    line-height: 40px;
    color: #2d3640;
    font-size: 14px;
    border-left: 5px solid #fff;
    padding: 0 15px;
    &:nth-child(2n-1) {
      background: #f4fbfe;
      border-left: 5px solid #f4fbfe;
    }
    &:hover {
      background: #ddf3fc;
      border-left: 5px solid #1eb1ee;
    }
  }
}
.pointerLeft {
