  top: 216px;
  padding: 10px 20px 10px 30px;
  box-sizing: border-box;
  color: #fff;
  display: none;
}
.tooltipBox h3 {
  font-size: 18px;
}
.tooltipBox p {
  font-size: 16px;
}
</style>



